	module.exports = {"title":"Jailbreak Wii (Softmod) 3.0  4.2","date":"2011-04-12 12:00","author":"Blake Embrey","layout":"article","body":"<p><img src=\"channel.png\" alt=\"Homebrew Channel\"></p>\n<p><strong>IMPORTANT: This information is for research and academic purposes only! This info is not to be abused! I am not responsible for any damage that you may create!</strong></p>\n<p>Although I did successfully complete this guide on my own Wii console, it does not mean that this same process will successfully jailbreak your Wii console. Make sure you read and complete all steps appropriately.</p>\n<p><strong>Prerequisites</strong></p>\n<ul>\n<li>Check your Wii version. This can be done by going to <code>Wii Settings -&gt; Top Right Corner -&gt; Your Wii's Firmware &amp; Region</code></li>\n<li>Jailbreak file [3.0-4.1]  <a href=\"http://www.mediafire.com/?rv3w0le0nrf7mm1\">Mediafire</a> (Password is <code>wiihacks</code>)</li>\n<li>Jailbreak file [4.2]  <a href=\"http://www.mediafire.com/?9ulaa2elqvu7z4z\">Mediafire</a> (Password is <code>wiihacks</code>)</li>\n<li>SD card with 1GB or more.</li>\n</ul>\n<p><strong>Preparation</strong></p>\n<ol>\n<li>Format your SD card to FAT32 or FAT16</li>\n<li>Make Sure WiiConnect 24 is OFF</li>\n<li>Unzip the previously downloaded jailbreak pack and place the files/folders onto the root of your SD card.</li>\n<li>Always launch md5summer.exe to verify that the files have been downloaded properly and they are not corrupted.</li>\n</ol>\n<p><strong>Step 1  Installing Homebrew Channel and Bootmii</strong></p>\n<p><strong>[3.0-4.1]</strong> Go to Wii Options, then go to <code>Data Management -&gt; Channels -&gt; SD Card</code>, click on <code>Bannerbomb channel</code>, you will get a message saying Load boot.elf/dol?, select Yes and the Hackmii installer will run.</p>\n<p><strong>[4.2]</strong> Click the SD Card Icon on the Wii Menu  Load boot.elf/dol?, select Yes and the Hackmii installer will run.</p>\n<p>In the Hackmii Installer, make sure to install the Homebrew Channel. If you can, also install Bootmii as boot2. When installing bootmii select prepare SD card, then install. Bootmii as IOS is installed automatically.</p>\n<p>(If you receive an error stating No vulnerable IOS, <a href=\"http://www.wiihacks.com/recommended-faqs-guides-tutorials-only/29080-hackmii-fix-no-vulnerable-ios-black-screen-scam-freeze.html\">complete these steps</a>.</p>\n<p><strong>Step 2  Backup Your Wiis NAND (required)</strong></p>\n<p>It is highly recommended to make a NAND Backup with bootmii as it can save you from 99% of bricks. That is, if you can install bootmii as boot2. Youll need a SD Card with at least 550 MB free on it. Also, note bootmii can be picky about SD Cards so its best to avoid generic ones. The 2GB Sandisk SD Card is recommended.</p>\n<p><strong>a)</strong> Place the SD Card into your wii and power it on. If bootmii was installed as boot2, the bootmii screen should automatically appear, if it was installed as an IOS, then youll have to go to homebrew channel, press home on your wiimote then select Launch Bootmii</p>\n<p><strong>b)</strong> Navigate using the power/reset buttons on the front of your console and select the icon with the gears on it. If the console buttons dont work, youll have to use a gamecube controller.</p>\n<p>Power = Browse\nReset = Select</p>\n<p><strong>c)</strong> Now select the icon that has the Green arrow pointing to a SD Card but from the chip. Then follow the on-screen instructions to backup your NAND.</p>\n<p><strong>d)</strong> In 10  15 minutes, the NAND Backup should be complete. You may get a few bad blocks but any number of bad blocks under 80 is normal and fine. Once all is done, you can exit bootmii.</p>\n<p><strong>e)</strong> Remove the SD Card from your wii and insert into your PC, backup the bootmii folder, the NAND.bin file and key.bin file from your sd card onto a safe place on your PC. Once these files/folders are on your PC, delete them from your SD Card. If you do not and bootmii as boot2 is installed, it will load everytime!</p>\n<p><strong>DO NOT LOSE YOUR NAND.BIN OR KEYS.BIN</strong></p>\n<p>If you were to ever fully brick the wii, just copy the bootmii folder and NAND.bin from your PC onto the root of your SD Card. Then repeat parts <code>a</code>, <code>b</code> &amp; <code>c</code> but this time selecting the icon that points to the chip. While <code>bootmii</code>/<code>boot2</code> can save you from full bricks, <code>bootmii</code>/<code>IOS</code> cannot! It is not recommended to use <code>bootmii</code> as an <code>IOS</code> to restore your NAND! If you can only install as an <code>IOS</code>, your nand backup is still useful, keep it safe.</p>\n<p><strong>Step 3  Modding your Wii (the jailbreak)</strong></p>\n<p><strong>[3.0-4.1 Only]</strong></p>\n<p><strong>a)</strong> Put the SD card into the Wii</p>\n<p><strong>b)</strong> Open The Homebrew channel and select Multi-Mod Manager. Go to <code>Install &amp; Patch iOS36 -&gt; Express Install</code> - When completed press <code>B</code> to go back into main menu go to the bottom choice <code>Load Another IOS</code> and select <code>IOS236</code> (this should already be selected).</p>\n<p>In Multi-Mod Manager go into <code>Wad Manager</code>, it should display these wads.</p>\n<p>(These are to softmod your Wii)</p>\n<p>iOS60-[6174]-Patched.wad\ncIOS249[56]-rev20.wad\ncIOS250[57]-rev65535.wad\ncIOS223[37+38]-rev4.wad\ncIOS222[38]-rev4.wad</p>\n<p>(These are to install the extra IOS files)</p>\n<p>iOS53-64-v5663.wad\niOS55-64-v5663.wad\niOS56-64-v5662.wad\niOS57-64-v5919.wad\niOS58-64-v6176.wad</p>\n<p><strong>c)</strong> Press 1 on Wiimote to install all these wads at once, Press A to confirm. If you get an error do not exit until they all install correctly. Retry installing</p>\n<p>After it installs correctly press the HOME button on Wiimote until youre back in main Multi-Mod Manager Menu.</p>\n<p><strong>[4.2 Only]</strong></p>\n<p><strong>a)</strong> SD card in the Wii\n<strong>b)</strong> Open The Homebrew channel and select Multi-Mod Manager. Go to <code>Install &amp; Patch iOS36 -&gt; Express Install</code> - Once completed press B to go back into main menu go to the bottom choice <code>Load Another IOS</code> and select <code>IOS236</code> (this should already be selected).</p>\n<p>In Multi-Mod Manager go into Wad Manager, it should display these wads.</p>\n<p>(These are to softmod your wii)</p>\n<p>iOS70-[6687]-Patched.wad\ncIOS249[56]-rev20.wad\ncIOS250[57]-rev65535.wad\ncIOS222[38]-rev4.wad\ncIOS223[37+38]-rev4.wad</p>\n<p>(These are the IOS your Wii needs)</p>\n<p>iOS56-64-v5662.wad\niOS57-64-v5919.wad\niOS58-64-v6176.wad</p>\n<p><strong>c)</strong> Press 1 on wiimote to install all these wads at once, Press A to confirm. If you get an error do not exit until they install correctly. Retry installing.</p>\n<p>After they have all installed correctly press the B button on Wiimote until youre back in main Multi-Mod Manager Menu.</p>\n<p><strong>Step 4  Installing Priiloader</strong></p>\n<p><code>HBC -&gt; Load up MMM</code>. Make sure where it says Load another ios [IOS236 is Selected]</p>\n<p><strong>a)</strong> <code>Multi-Mod Manager -&gt; App Manager -&gt; Select Priiloader</code></p>\n<p><strong>b)</strong> It Should Load up Press + to Install</p>\n<p><strong>c)</strong> Your Wii should reboot into Homebrew Channel after the installation. Press the HOME button and power off your Wii. Hold reset on the console while turning it on to boot into Priiloader. Go to System Menu Hacks and enable Block Disc Updates &amp; Block Online Updates, select Save Settings when finished. Press B on the Wiimote to go back to the main menu, next go to Settings and use the d-pad to set Autoboot: System Menu. Then select Save Settings. Press B to go back to the main menu.</p>\n<p><strong>d)</strong> In the main menu in priiloader, select Launch Title and select the HomeBrew Channel to check if its working properly.</p>\n<p><strong>Step 5  Getting HBC to use iOS58 for USB2 Support</strong></p>\n<p>From the Homebrew channel load up the hackmii installer and reinstall the Homebrew channel. This will make it use ios58 for USB2 <a href=\"http://Support.To\">Support.To</a> check your HBC is using iOS58 press the home button when in HBC and it should tell you in top right corner.</p>\n<p><strong>Step 6  Install more apps</strong></p>\n<p>This is what the whole jailbreak process was for, installing custom apps. Here is a list of some awesome apps, starting at my favourite.</p>\n<ul>\n<li><a href=\"http://www.wiimc.org/downloads/\">WiiMC</a>  Wii Media Center</li>\n<li><a href=\"http://www.wiihacks.com/other-faq-guides-tutorials/68251-guide-homebrew-browser.html\">Homebrew Browser</a> - Wii Homebrew App Store</li>\n<li>Update the <a href=\"http://www.wiihacks.com/general-homebrew-hack-issues/67034-update-shopping-channel-v20-guide.html\">Wii Shopping Channel</a> to v20</li>\n<li><a href=\"http://www.wiihacks.com/recommended-faqs-guides-tutorials-only/47802-play-gamecube-backups-your-wii.html\">Play Gamecube games</a> on your Wii</li>\n<li><a href=\"http://www.wiihacks.com/emulators-downloads/\">Emulators</a></li>\n<li><a href=\"http://www.wiihacks.com/other-faq-guides-tutorials/66235-ftpii-guide.html\">Wii FTP</a></li>\n</ul>\n<p><strong>And you are done!</strong></p>\n<p>Special thanks to author, <em>Cile</em>, for the original guide over at <a href=\"http://www.wiihacks.com/recommended-faqs-guides-tutorials-only/69690-softmod-3-0-4-2-e-u-j-wiis.html\">WiiHacks</a>.</p>\n"}

