	module.exports = {"title":"Change Desktop Background with Shell and GeekTool","date":"2011-10-30 12:00","author":"Blake Embrey","layout":"article","body":"<p>Make an AppleScript and run it using the Shell geeklet provided in GeekTool. First, well make the AppleScript. So, open the AppleScript Editor app to get started. Copy and paste this code:</p>\n<pre><code>tell application \"Finder\"\n  set desktop picture to POSIX file \"file://localhost/  yourimage.png\"\nend tell\n</code></pre>\n<p>Then, replace <code>file://localhost/yourimage.png</code> with the location to your image. It must be an absolute path to the image, so you might want to make a folder just for your GeekTool scripts somewhere. For example, I want to use the Tron background image, so I used <code>file://localhost/Users/blakeembrey/Dropbox/Application Settings/GeekTool/TronLegacy.png</code>. I am currently storing my GeekTool scripts in an Applications folder inside my documents, as you can see. Once I finished that, I saved the script as <code>setTronBackground</code> in the same folder.</p>\n<p>Now in Geektool, drag the shell geeklet to the desktop from GeekTool. It doesnt matter where it goes since it isnt going to be seen anyway. Set the group you would like it to be a part of, then set the Refresh Every to 0. For the shell code, we are going to use something like</p>\n<pre><code>osascript ~/Documents/Applications/GeekTool/setTronBackground.scpt\n</code></pre>\n<p>Notice we can use the relative location here, and that we need .scpt for the file extension. Finally, save and close GeekTool. Now every time you change the theme you will also change the background image.</p>\n"}

