	module.exports = {"title":"Simplify Local Development with Dnsmasq","date":"2012-04-24 07:28","author":"Blake Embrey","layout":"article","body":"<p>Ever wanted to be able to set up your local domains instantly and never have to worry about your <code>/etc/hosts</code> again? Me too! A bit of looking around and I discovered a wealth of information about a small <em>DNS Forwarder</em> called DNSMASQ.</p>\n<p>So, this is great. We have our solution - its a tiny program barely taking 700KB of RAM, and its a cinch to set up. By the end of this post, you will have a working TLD for use with your local development applications. No more playing with your <code>/etc/hosts</code> file every time you want to add a new domain.</p>\n<p>In this tutorial, I will be focusing on Mac as it is the OS I use, however, the instructions should be fairly similar on linux. It has been tested in Mountain Lion and should work without a hiccup all the way down to Leopard (probably lower).</p>\n<p>First of all, if you havent installed it already, we will install <strong>homebrew</strong> - the missing package manager for OS X. To install, just follow the instructions available on <a href=\"http://mxcl.github.com/homebrew/\">the homepage</a> or just copy and paste this snippet into terminal:</p>\n<pre><code>/usr/bin/ruby -e \"$(/usr/bin/curl -fksSL https://raw.github.com/mxcl/homebrew/master/Library/Contributions/install_homebrew.rb)\"\n</code></pre>\n<p>Once brew is installed, well install dnsmasq using <code>brew install dnsmasq</code></p>\n<p>Next, lets activate dnsmasq. Homebrew should have output some hints on how to get started, but in case you missed it  should be something along these lines.</p>\n<pre><code>cp /usr/local/Cellar/dnsmasq/2.57/dnsmasq.conf.example /usr/local/etc/dnsmasq.conf\nsudo cp /usr/local/Cellar/dnsmasq/2.57/homebrew.mxcl.dnsmasq.plist /Library/LaunchDaemons\nsudo launchctl load -w /Library/LaunchDaemons/homebrew.mxcl.dnsmasq.plist\n</code></pre>\n<p>Brilliant, now lets modify our configuration file at <code>/usr/local/etc/dnsmasq.conf</code>:</p>\n<p>Well add two rules. The first is the address, or TLD, we will listen to. In this case we will use <code>.dev</code>, but you can use anything (except <code>.local</code> - <a href=\"http://www.justincarmony.com/blog/2011/07/27/mac-os-x-lion-etc-hosts-bugs-and-dns-resolution/\">not a good idea</a>)</p>\n<pre><code>address=/dev/127.0.0.1\nlisten-address=127.0.0.1\n</code></pre>\n<p>To start and stop dnsmasq, simply use</p>\n<pre><code>sudo launchctl stop homebrew.mxcl.dnsmasq && sudo launchctl start homebrew.mxcl.dnsmasq\n</code></pre>\n<p>However, I found it even easier to just kill the script in the Activity Monitor. For the final step, add <code>127.0.0.1</code> as a name server in <code>System Preferences -&gt; Network -&gt; Advanced -&gt; DNS</code>. You can add it along with some other DNS records, just make sure its at the top. For example, I use Googles Public DNS - <code>8.8.8.8</code> and <code>8.8.4.4</code>.</p>\n<p>Now in whichever language you are writing in, you should be able to easily set up your virtual hosts for use on the <code>.dev</code> TLD. You should be even able to see your name server setup using <code>scutil --dns</code>.</p>\n<p>References:</p>\n<ul>\n<li><a href=\"http://serverfault.com/a/164215\">ServerFault Question</a></li>\n<li><a href=\"http://blog.philippklaus.de/2012/02/install-dnsmasq-locally-on-mac-os-x-via-homebrew/\">Install DNSmasq locally on Mac OS X via Homebrew</a></li>\n</ul>\n"}

