	module.exports = {"title":"Contributing to Open Source","date":"2013-04-03 20:35","author":"Blake Embrey","layout":"article","body":"<blockquote>\n<p>Software is like sex; its better when its free. - Linus Torvalds</p>\n</blockquote>\n<p>Being able to contribute to open source is an awesome feeling. The feeling of having code which thousands, if not millions, of people will run everyday is enough to make you cringe. Surprisingly though, few people realize how empowering, yet simple, contributing to an open source project can be.</p>\n<p>In this article I am going to run you through what my usual process looks like when issuing a pull request through GitHub, and what sort of things to expect.</p>\n<h2>First Steps</h2>\n<p>The first thing youll need is an open source library or project that you are passionate about. Finding a library that fits this criteria can be difficult, but I am sure there are a dozen of projects you take for granted everyday. Its unlikely you even realize how many issues some libraries have backlogged to work through.</p>\n<p>Once you have found a project you are interested in contributing to, chances are its on GitHub. If its not on GitHub, repeat the previous step. For the purpose of this blog post, Ill be running with <a href=\"https://github.com/LearnBoost/stylus/issues/989\">this issue from Stylus</a>.</p>\n<p><img src=\"http://f.cl.ly/items/2n2R1S3M291X3V1k110C/CapturFiles_1.png\" alt=\"Stylus issue on GitHub\"></p>\n<p>The first thing youll need to do is fork the repo. This is done by clicking the <code>Fork</code> button in the top right of GitHub. Once you fork the repo, youll have your own personal copy of the original repo that you can do what you want with. From there, youll want to use you command line to clone the repo off GitHub.</p>\n<p><img src=\"http://f.cl.ly/items/0s0V12453x0X3Z102i1Z/CapturFiles_3.png\" alt=\"Local Stylus Fork\"></p>\n<pre><code>git clone git@github.com:blakeembrey/stylus.git\n</code></pre>\n<p>After cloning the repository, youll now have a local copy on your machine. My next step is usually to set up the <code>upstream</code> repository link for pulling down code.</p>\n<pre><code>git remote add upstream git@github.com:LearnBoost/stylus.git\n</code></pre>\n<p>The next step is to create a branch, normally based on the name or description of the issue you are trying to solve. You dont want to make your changes in the <code>master</code> branch as it will make future updates and pull requests more difficult to do.</p>\n<pre><code>git checkout -b media-query-colon\n</code></pre>\n<p>With that command, Ill quickly create a new branch based off <code>master</code> and switch to it. The first thing youll want to do before writing any code is try and find the test suite for the project (hopefully they have one). This is the most useful thing I find during development, especially when its a library Im not entire familiar with. With Stylus, I know I can find the test script under the scripts section the <code>package.json</code>.</p>\n<pre><code>npm test\n</code></pre>\n<p><img src=\"http://f.cl.ly/items/0f0x1n22031F3j3E060D/CapturFiles_4.png\" alt=\"Command Line after working through the previous steps\"></p>\n<h2>Committing your work</h2>\n<p>Running the test script should quickly give an overview of how the library is constructed and the current status of the library. Most of the time, all tests should be passing - otherwise this would mean more bugs that need fixing.</p>\n<p>After running the test script, I usually find it useful to whip up a new test based on the issue you are trying to solve - which will be failing initially. This is known as Test Driven Development (TDD), and will be crucial as you explore the core of some libraries. Make sure to add as many test cases to demostrate the issue and fix, which will also stop future regressions.</p>\n<blockquote>\n<p>In lieu of a formal styleguide, take care to maintain the existing coding style.</p>\n</blockquote>\n<p>When contributing to someone elses library, youll want to make sure your coding style is matching the code which already exists. This is an important concept to understand, as it makes future commits easier to read through when all the code is consistent. <a href=\"https://github.com/rwldrn/idiomatic.js/#all-code-in-any-code-base-should-look-like-a-single-person-typed-it-no-matter-how-many-people-contributed\">All code in any code-base should look like a single person typed it, no matter how many people contributed</a>. You may find that some projects also include a <a href=\"https://github.com/twitter/bootstrap/blob/master/CONTRIBUTING.md\">CONTRIBUTING.md</a> file.</p>\n<p>Somewhere during this time youll want to commit your changes. Next, well want to push our changes back to the remote repository we cloned from earlier (<code>git push -u origin media-query-colon</code>). This will create a new branch on the remote repo to track from. Once the code is pushed back up to GitHub, you can create a pull request from your repo back to the original (<code>upstream</code>).</p>\n<h2>Creating a pull request</h2>\n<p>When creating the pull request, it is vital to provide a descriptive title and contents - anyone reading the issue should understand what your trying to achieve before reading any code. If there are similar issues or you were fixing an existing issue, make sure to take a note of the issue numbers in the issue description. You can even make a reference to another repo if it affects it - this shows the impact of your change and demostrates the priority of your patch.</p>\n<h2>Keeping it relevant</h2>\n<p>Chances are, by now there have been new commits created since you forked the repo, or perhaps your commit history for the issue is just getting a little unwieldly. Fortunately there is a simple utility built into git for this known as rebasing.</p>\n<pre><code>git checkout master\ngit pull upstream master\ngit checkout media-query-colon\ngit rebase -i master # Interactively rebase\n</code></pre>\n<p><img src=\"http://f.cl.ly/items/1k3N1Q1I282p3Y3J233L/CapturFiles_6.png\" alt=\"Rebase process\"></p>\n<p>A rebase will rewrite your commit history, so you can squash commits together into a single commit and clean up all the changes you have made. Itll also move the commits to be the lastest in the commit log. Because the history will be rewritten, next time you push youll probably need to use the force push flag which overrides the remote commit (<code>git push -f</code>).</p>\n<p>Luckily, with the way GitHub works, any commits in the branch - new or old -  will be appended to your pull request. This makes is simple to quickly iterate on any issues raised by fellow developers or the project owner.</p>\n<h2>Completion</h2>\n<p>Now you are likely done. Depending on the repo and repo owner(s), it could take anywhere from a few minutes to a few months for your pull request to be reviewed. It wont always be perfect, and it may be denied. In the case of your pull request being denied, check out the feedback - itll usually be really useful and maybe even give you an idea of where to go from there. If not, there are always more repos in need of contributors.</p>\n"}

