	module.exports = {"title":"Improving Your JavaScript Console Skills","date":"2013-07-05 10:00","author":"Blake Embrey","layout":"article","body":"<p><em>This article is meant as an introduction to some of the features of the JavaScript console. It is not all encompassing, but I will try to cover all the features I find myself using and others that I could not live without. All the features mentioned will be tailored to the Chrome Dev Tools, but I will try to mention any differences with other modern browsers.</em></p>\n<h2>Accessing the dev tools</h2>\n<p>If you have never used the developer tools before, you may have problems finding them. The easiest and simplest way to access the developer tools is by using the keyboard shortcuts. On Mac, this is <code>Command + Option + I</code>. On Windows, itll be <code>F12</code>. In Safari, youll have to enable the developer tools under preferences first.</p>\n<p>You can also inspect an element directly by right clicking and choosing <code>Inspect Element</code>, and you can always access the dev tools from the menu shortcuts.</p>\n<h2>Console Functions</h2>\n<p>The console global is an object that provides some useful shortcuts for debugging JavaScript. Arguably the most used function is <code>console.log</code>, but did you know the console also provides <code>info</code>, <code>warn</code>, <code>debug</code> and <code>error</code> functions. They operate in the same way as the <code>log</code> function, but the visual output will be altered and <code>error</code> output an accompanying stack trace.</p>\n<p><img src=\"console-functions.png\" alt=\"Console Functions Demonstrated\"></p>\n<p>All these functions accepts an unlimited number of parameters, and will be formatted for output in the console in different ways - depending on the object types there are. For example, DOM nodes will output as a representation of the node from the elements tab while array-like objects will appear as an array.</p>\n<p>However, this added formatting convenience can sometimes be a pain. This is especially relevant when you want to see the underlying structure of a DOM node or an object. You may not even know it, but you have probably needed <code>console.dir</code> at some point because it does exactly this.</p>\n<p><img src=\"console-dir-vs-log.png\" alt=\"Difference between console.log and console.dir\"></p>\n<h2>Manipulating Nodes from the Elements Tab</h2>\n<p>Weve all been there - playing with a DOM node in the elements tab, but now we need to use JavaScript on it. We could do a regular DOM traversal to find it, but then we have to remember the ID or tag name, maybe even some obscure combination involving classes. If you switch to the Console tab on the Developer Tools, you can always grab the latest node you had selected using the <code>$0</code> variable shortcut. In Chrome, this can even be expanded upon up to <code>$4</code> - thats the latest five elements you had selected.</p>\n<p><code>$0</code> - <code>$4</code> are direct references to the DOM nodes, so you can start manipulating it directly by grabbing the inner text (<code>$0.innerText</code>) or passing it to jQuery for manipulation (<code>$($0)</code>). While on the subject, did you know Chrome also provides an alias to <code>document.querySelectorAll</code> under <code>$$</code> and <code>document.querySelector</code> under <code>$</code> (if jQuery hasnt overwritten it of course).</p>\n<h2>Other Useful Console Functions</h2>\n<p>Dont despair yet, Chrome still has a wealth of functions at your debugging disposal. For your timing purposes, there is <code>console.time</code> and <code>console.timeEnd</code>. Each accept a single string as the name of the timer to stop and start. Even cooler is <code>console.assert</code>, which accepts two parameters - the check and a string - and logs an assertion failure with the string when the check is falsy.</p>\n<p>An interesting addition is <code>console.table</code>, which allows you to log an array of similar objects in a readable fashion. It accepts the array of objects as the first parameter, but also accepts a second parameter as the object keys you want to log.</p>\n<p><img src=\"console-table.png\" alt=\"Demonstrating console.table\"></p>\n<h2>Using the Console</h2>\n<p>Whenever you are working in the console, hitting <code>Enter</code> will cause the content of the console to be evaluated and output to the screen. You can optionally use <code>Shift + Enter</code> to work over multiple lines, then just use <code>Enter</code> as usual to trigger evaluation. If you want to continue working on the code that was previously evaluated, just press the <code>Up</code> arrow - just like using the command line. The result of the previous evaluation is even stored in the variable <code>$_</code>, so you can use it in your next evaluation.</p>\n<h2>Other Tips and Tricks</h2>\n<p>The Chrome console also provides a function called <code>monitorEvents</code> that works exactly as described. It accepts an DOM node to monitor as the first argument and by default itll monitor and log all the events. Optionally, you could pass in the string <code>&quot;mouse&quot;</code> or <code>&quot;key&quot;</code> to log only mouse or keyboard based events.</p>\n<p>Conveniently, Chrome also provides an <code>inspect</code> method which I feel is almost the opposite to <code>$0</code>. It will open the elements panel and focus on the element within the DOM tree. There is even a <code>copy</code> method to copy stuff to your clipboard.</p>\n<p>Somewhat less used, but Chrome also provides a <code>keys</code> function - which maps to <code>Object.keys</code> - and a <code>values</code> function, which is the polar opposite of keys.</p>\n<p>And if at some point you just feel like a chump using your mouse and clicking to clear the console, itll be welcome news that there is even a <code>clear</code> function that can do it for you (and <code>Ctrl + L</code> keyboard shortcut!). And remember, you can always persist the console over page reloads by right clicking and selecting Preserve Log upon Navigation.</p>\n"}

