	module.exports = {"title":"My Sublime Text Preferences Explained","date":"2013-08-11 22:20","author":"Blake Embrey","layout":"article","body":"<p>Prompted by the many preference file posts recently, I have finally gotten around to posting my config with a few inline comments.</p>\n<pre><code class=\"language-javascript\">{\n  <span class=\"hljs-comment\">// Makes folders stand out from files</span>\n  <span class=\"hljs-string\">\"bold_folder_labels\"</span>: <span class=\"hljs-literal\">true</span>,\n  <span class=\"hljs-comment\">// Clean colour scheme, comes with Flatland theme</span>\n  <span class=\"hljs-string\">\"color_scheme\"</span>: <span class=\"hljs-string\">\"Packages/Theme - Flatland/Flatland Monokai.tmTheme\"</span>,\n  <span class=\"hljs-comment\">// Switch to `all` to always show invisibles</span>\n  <span class=\"hljs-string\">\"draw_white_space\"</span>: <span class=\"hljs-string\">\"selection\"</span>,\n  <span class=\"hljs-comment\">// Always adds a trailing newline character</span>\n  <span class=\"hljs-string\">\"ensure_newline_at_eof_on_save\"</span>: <span class=\"hljs-literal\">true</span>,\n  <span class=\"hljs-comment\">// Exludes directories from showing up in the sidebar and search</span>\n  <span class=\"hljs-string\">\"folder_exclude_patterns\"</span>: [\n    <span class=\"hljs-string\">\".svn\"</span>,\n    <span class=\"hljs-string\">\".git\"</span>,\n    <span class=\"hljs-string\">\".hg\"</span>,\n    <span class=\"hljs-string\">\"CVS\"</span>,\n    <span class=\"hljs-string\">\"node_modules\"</span>,\n    <span class=\"hljs-string\">\"bower_components\"</span>\n  ],\n  <span class=\"hljs-comment\">// Source Code Pro is my favourite monospace font - download at https://github.com/adobe/source-code-pro</span>\n  <span class=\"hljs-string\">\"font_face\"</span>: <span class=\"hljs-string\">\"Source Code Pro\"</span>,\n  <span class=\"hljs-comment\">// Current working font size</span>\n  <span class=\"hljs-string\">\"font_size\"</span>: <span class=\"hljs-number\">15.0</span>,\n  <span class=\"hljs-comment\">// Highlight the current line - makes it easier to focus and find where you are typing</span>\n  <span class=\"hljs-string\">\"highlight_line\"</span>: <span class=\"hljs-literal\">true</span>,\n  <span class=\"hljs-comment\">// Make it obvious when I haven't saved something</span>\n  <span class=\"hljs-string\">\"highlight_modified_tabs\"</span>: <span class=\"hljs-literal\">true</span>,\n  <span class=\"hljs-comment\">// Ignore VIM mode, it plays havoc with some of my other preferred shortcuts</span>\n  <span class=\"hljs-string\">\"ignored_packages\"</span>: [\n    <span class=\"hljs-string\">\"Vintage\"</span>\n  ],\n  <span class=\"hljs-comment\">// Draw all indent guides, but also draw the carets indent guide in a different color</span>\n  <span class=\"hljs-string\">\"indent_guide_options\"</span>: [\n    <span class=\"hljs-string\">\"draw_active\"</span>,\n    <span class=\"hljs-string\">\"draw_normal\"</span>\n  ],\n  <span class=\"hljs-comment\">// Removes the extra whitespace using `Source Sans Pro` comes with</span>\n  <span class=\"hljs-string\">\"line_padding_bottom\"</span>: <span class=\"hljs-number\">-1</span>,\n  <span class=\"hljs-string\">\"line_padding_top\"</span>: <span class=\"hljs-number\">-1</span>,\n  <span class=\"hljs-comment\">// Render a vertical ruler at the 80 character mark, tries to keep me considerate</span>\n  <span class=\"hljs-string\">\"rulers\"</span>: [\n    <span class=\"hljs-number\">80</span>\n  ],\n  <span class=\"hljs-comment\">// Saving everytime I lose focus causes all sorts of conflicts</span>\n  <span class=\"hljs-string\">\"save_on_focus_lost\"</span>: <span class=\"hljs-literal\">false</span>,\n  <span class=\"hljs-comment\">// Always allow me to scroll beyond the last line</span>\n  <span class=\"hljs-string\">\"scroll_past_end\"</span>: <span class=\"hljs-literal\">true</span>,\n  <span class=\"hljs-comment\">// Enable `shift + tab` to cause unindent</span>\n  <span class=\"hljs-string\">\"shift_tab_unindent\"</span>: <span class=\"hljs-literal\">true</span>,\n  <span class=\"hljs-comment\">// Override tab size to equal 2 spaces</span>\n  <span class=\"hljs-string\">\"tab_size\"</span>: <span class=\"hljs-number\">2</span>,\n  <span class=\"hljs-comment\">// Beautiful and minimal theme - download at https://github.com/thinkpixellab/flatland</span>\n  <span class=\"hljs-string\">\"theme\"</span>: <span class=\"hljs-string\">\"Flatland Dark.sublime-theme\"</span>,\n  <span class=\"hljs-comment\">// Use spaces for indentation</span>\n  <span class=\"hljs-string\">\"translate_tabs_to_spaces\"</span>: <span class=\"hljs-literal\">true</span>,\n  <span class=\"hljs-comment\">// Removes all the trailing white space on save</span>\n  <span class=\"hljs-string\">\"trim_trailing_white_space_on_save\"</span>: <span class=\"hljs-literal\">true</span>,\n  <span class=\"hljs-comment\">// Wraps lines instead of enabling horizontal scroll</span>\n  <span class=\"hljs-string\">\"word_wrap\"</span>: <span class=\"hljs-literal\">true</span>\n}\n</code></pre>\n"}

