	module.exports = {"title":"Writing a Github Pages Deploy Script","date":"2013-08-04 20:40","author":"Blake Embrey","layout":"article","body":"<p>Lately I have found myself creating more and more sites of the static kind and deploying them Github Pages. Regularly, this would involve having a <code>public</code> directory which contains all the assets before I build them (think preprocessor - SASS, Less, Stylus, Jade, Browserify, Requirejs) and another <code>build</code> directory where these assets are compiled to during development, testing and deployment. I found myself doing this so often, I figured Id better make a little Makefile script for me to easily deploy any directory to Github Pages.</p>\n<pre><code class=\"language-bash\">deploy:\n  @grunt build\n  @<span class=\"hljs-built_in\">cd</span> ./build &amp;&amp; git init . &amp;&amp; git add . &amp;&amp; git commit -m \\<span class=\"hljs-string\">\"Deploy\\\" &amp;&amp; \\\n  git push \"</span>git@github.com:blakeembrey/&lt;repo&gt;.git<span class=\"hljs-string\">\" master:gh-pages --force &amp;&amp; rm -rf .git\n</span></code></pre>\n<p>All the script does is run the build, in this case <code>grunt build</code> and initialises the <code>build</code> directory as a git repository. It then commits all the directory contents and pushes it up to the Github Pages branch of a repo you define.</p>\n"}

