	module.exports = {"title":"Introduction to Browserify","date":"2013-09-12 22:00","author":"Blake Embrey","layout":"article","body":"<blockquote>\n<p>require(modules) in the browser.</p>\n</blockquote>\n<p><a href=\"https://github.com/substack/node-browserify\">Browserify</a> is a development tool that allows us to write node.js-style modules that compile for use in the browser. Just like node, we write our modules in separate files, exporting external methods and properties using the <code>module.exports</code> and <code>exports</code> variables. We can even require other modules using the <code>require</code> function, and if we omit the relative path itll resolve to the module in the <code>node_modules</code> directory.</p>\n<h2>Getting Started</h2>\n<p>Getting started with the browserify command-line tool requires node.js and npm installed.</p>\n<pre><code class=\"language-bash\">npm install browserify -g\n</code></pre>\n<h2>Our First Module</h2>\n<p><strong>multiply.js:</strong></p>\n<pre><code class=\"language-javascript\"><span class=\"hljs-built_in\">module</span>.exports = <span class=\"hljs-function\"><span class=\"hljs-keyword\">function</span> (<span class=\"hljs-params\">a, b</span>) </span>{\n  <span class=\"hljs-keyword\">return</span> a * b;\n};\n</code></pre>\n<p><strong>square.js:</strong></p>\n<pre><code class=\"language-javascript\"><span class=\"hljs-keyword\">var</span> multiply = <span class=\"hljs-built_in\">require</span>(<span class=\"hljs-string\">'./multiply'</span>);\n\n<span class=\"hljs-built_in\">module</span>.exports = <span class=\"hljs-function\"><span class=\"hljs-keyword\">function</span> (<span class=\"hljs-params\">n</span>) </span>{\n  <span class=\"hljs-keyword\">return</span> multiply(n, n);\n};\n</code></pre>\n<p><strong>index.js:</strong></p>\n<pre><code class=\"language-javascript\"><span class=\"hljs-keyword\">var</span> square = <span class=\"hljs-built_in\">require</span>(<span class=\"hljs-string\">'./square'</span>);\n\n<span class=\"hljs-built_in\">console</span>.log(square(<span class=\"hljs-number\">125</span>)); <span class=\"hljs-comment\">//=&gt; 15625</span>\n</code></pre>\n<p>Now we have written a couple of modules that require each other, we can run browserify and generate the file for use in the browser:</p>\n<pre><code class=\"language-bash\">browserify index.js -o bundle.js\n</code></pre>\n<p>Now that we have a <code>bundle.js</code> file that bundled the three modules we wrote, we can add a single script tag reference to it into our html page and itll execute in the browser automatically resolving <code>require</code> calls. <code>&lt;script src=&quot;bundle.js&quot;&gt;&lt;/script&gt;</code> and we should see <code>15625</code> logged to the JavaScript console.</p>\n<h2>NPM + Browserify</h2>\n<p>Since browserify implements the node.js module resolve algorithm, we can easily use npm to install modules from the package manager and use them inside the browser. There are lots of modules on npm that are made for tools such as browserify, but even more exciting is watching modules that were specifically written for node running in a browser environment without any effort. Lets install <code>underscore</code> and include it in our script.</p>\n<pre><code class=\"language-bash\">npm install underscore --save\n</code></pre>\n<pre><code class=\"language-javascript\"><span class=\"hljs-keyword\">var</span> _ = <span class=\"hljs-built_in\">require</span>(<span class=\"hljs-string\">'underscore'</span>);\n\n_.each([<span class=\"hljs-number\">1</span>, <span class=\"hljs-number\">2</span>, <span class=\"hljs-number\">3</span>], <span class=\"hljs-function\"><span class=\"hljs-keyword\">function</span> (<span class=\"hljs-params\">n</span>) </span>{\n  <span class=\"hljs-built_in\">console</span>.log(n); <span class=\"hljs-comment\">//=&gt; 1, 2, 3</span>\n});\n</code></pre>\n<h2>Node-core Modules</h2>\n<p>The biggest attraction of browserify over similar tools would have to be the inclusion of <a href=\"https://github.com/substack/node-browserify#compatibility\">node.js core modules</a>. Modules such as <code>url</code>, <code>path</code>, <code>stream</code>, <code>events</code> and <code>http</code> have all been ported for use in the browser. We cant do everything that node can do, but we can do everything a browser can do using node.js style code.</p>\n<p>The most immediately obvious core modules that are useful on the client-side are <code>querystring</code>, <code>url</code> and <code>path</code>. By requiring these core modules, we can easily parse and resolves urls, query strings and paths in a client script. On top of that, the <code>process</code>, <code>Buffer</code>, <code>__dirname</code>, <code>__filename</code> and <code>global</code> variables are all populated with Browserify. That means we can use <code>process.nextTick</code> to easily invoke a function on the next event loop (with full cross-browser support). A special <code>process.browser</code> flag is also set in browserify builds, so we can do a quick check to see if the script is running in a browser environment (as opposed to node.js for all the cross-environment module developers).</p>\n<h2>Transforms</h2>\n<p>The most powerful feature in Browserify are <a href=\"https://github.com/substack/node-browserify#list-of-source-transforms\">source transforms</a>. A source transform is a stream injected between the resolved module and the content that is returned. A simple use case for using a source transform is compiling CoffeeScript to JavaScript. Using <a href=\"https://github.com/substack/coffeeify\">coffeeify</a> there is no longer a need for precompilation steps, it just works.</p>\n<p>There are loads more transforms and you can easily write your own. Some transforms I find myself using regularly are <a href=\"https://github.com/substack/brfs\">brfs</a> (inlines file contents), <a href=\"https://github.com/epeli/node-hbsfy\">hbsfy</a> (precompile Handlebars templates, <em>better performance and smaller footprint</em>), <a href=\"https://github.com/hughsk/uglifyify\">uglifyify</a> (uglify bundled modules with UglifyJS2) and <a href=\"https://github.com/hughsk/envify\">envify</a> (use environment variables within modules).</p>\n<h2>Advanced Options</h2>\n<h3>Debug</h3>\n<p>Using the <code>-d</code> flag with Browserify will enable source map support. Source maps allow us to view the file in all its natural, multiple file glory. Just make sure you have source maps enabled in your dev tools, and debugging compiled scripts will become 100x easier.</p>\n<h3>Stand-alone</h3>\n<p>With the <code>-s &lt;name&gt;</code> option, we can create a bundle for public API consumption with other browser compile and runtime tools. It uses a UMD snippet to define an AMD module, CommonJS module and even falls back to aliasing the name to the <code>window</code> global.</p>\n<h3>External</h3>\n<p>In a production website environment, youll probably want to cut down on duplicate code being included by different modules. This can be done by using the <code>-x</code> flag, which specifies a module that should not be bundled directly with the build and instead required from the page itself. Combine this with the <code>-r</code> flag to explicitly require modules into a bundle, we can factor out common module dependencies and create a separate bundle.</p>\n<h3>Browserify Shim</h3>\n<p>Youll probably find some module that cant simply be required because it was written for the browser environment. Conviently, we can write a simple polyfill using <code>module.exports = window.$</code> or similar. But what if it has dependencies in the global that we have used <code>require</code> with? We could alter the snippet a bit more and alias required modules, but even easier is the <a href=\"https://github.com/thlorenz/browserify-shim\">browserify-shim</a> module that was written specifically with this purpose in mind.</p>\n<h3>Grunt task</h3>\n<p>Lots of people use Grunt everyday to run their build scripts, and browserify is no exception. <a href=\"https://github.com/jmreidy/grunt-browserify\">Grunt-browserify</a> provides an awesome grunt configuration for setting up your browserify builds and even comes with some extra sugar on top, such as a bundled <code>browserify-shim</code> config option.</p>\n<h3>Browser Field (package.json)</h3>\n<p>Browserify also supports the <a href=\"https://gist.github.com/shtylman/4339901\">browser</a> field in <code>package.json</code> files. This allows module developers to specify specific files that should be used in browser builds, in the case that the module has node-specific code that cant or shouldnt be browserified.</p>\n<h2>Next Steps</h2>\n<p>This has been a very brief introduction to Browserify and I havent even covered everything that is possible. The <a href=\"https://github.com/substack/node-browserify\">browserify docs</a> cover plenty of information and additional flags, so definitely take a quick look. Feel free to leave a comment with any issues you have, Id love to help out and write a follow up post that covers more uses.</p>\n"}

