	module.exports = {"title":"The JavaScript Bind Function","date":"2013-12-31 01:00","author":"Blake Embrey","layout":"article","body":"<p>The JavaScript <code>bind</code> function is a common-place utility when working with many different frameworks and libraries. Its purpose is to bind the <code>this</code> value to a static object and is useful when passing functions around as callbacks, where maintaining the correct <code>this</code> value is required. A common convention to circumvent this utility is the <code>var that = this</code>, but this isnt very feasible everywhere.</p>\n<p>In <a href=\"https://developer.mozilla.org/en-US/docs/Web/JavaScript/Reference/Global_Objects/Function/bind\">modern JavaScript implementations</a> the function is built directly onto <code>Function.prototype</code>, giving us <code>bind</code> functionality on every function. For our implementation well be implementing a standalone functionality that works similar to the built-in <code>bind</code> function.</p>\n<p>However, its important to note that <code>bind</code> also comes with another handy feature. It accepts an unlimited number of arguments after the context to pass in as the function parameters, from left to right.</p>\n<pre><code class=\"language-javascript\"><span class=\"hljs-keyword\">var</span> slice = <span class=\"hljs-built_in\">Array</span>.prototype.slice;\n\n<span class=\"hljs-comment\">/**\n * Bind a function to a certain context.\n *\n * @param  {Function} fn\n * @param  {Object}   context\n * @param  {*}        ...\n * @return {Function}\n */</span>\n<span class=\"hljs-function\"><span class=\"hljs-keyword\">function</span> <span class=\"hljs-title\">bind</span> (<span class=\"hljs-params\">fn, context <span class=\"hljs-regexp\">/*, ...args */</span></span>) </span>{\n  <span class=\"hljs-keyword\">var</span> args = slice.call(<span class=\"hljs-built_in\">arguments</span>, <span class=\"hljs-number\">2</span>);\n\n  <span class=\"hljs-keyword\">return</span> <span class=\"hljs-function\"><span class=\"hljs-keyword\">function</span> (<span class=\"hljs-params\"></span>) </span>{\n    <span class=\"hljs-keyword\">return</span> fn.apply(context, args.concat(slice.call(<span class=\"hljs-built_in\">arguments</span>)));\n  };\n};\n</code></pre>\n<p>Bind allows us to keep the <code>this</code> context when passing the callback to another function. Imagine passing a function that uses <code>this</code> into <code>setTimeout</code> or someone elses library utility, where <code>this</code> could be unpredictable.</p>\n<pre><code class=\"language-javascript\"><span class=\"hljs-function\"><span class=\"hljs-keyword\">function</span> <span class=\"hljs-title\">greet</span> (<span class=\"hljs-params\">greeting</span>) </span>{\n  <span class=\"hljs-keyword\">return</span> greeting + <span class=\"hljs-string\">' '</span> + <span class=\"hljs-keyword\">this</span>.user;\n};\n\ngreet(<span class=\"hljs-string\">'Hello'</span>); <span class=\"hljs-comment\">//=&gt; \"Hello undefined\"</span>\n\n<span class=\"hljs-keyword\">var</span> boundGreet = bind(greet, { <span class=\"hljs-attr\">user</span>: <span class=\"hljs-string\">'Bob'</span> });\n\nboundGreet(<span class=\"hljs-string\">'Hello'</span>); <span class=\"hljs-comment\">//=&gt; \"Hello Bob\"</span>\n</code></pre>\n<p>We also have another useful feature built into <code>bind</code> - partial application. Partial application is essentially the act of pre-filling function arguments. Any future arguments are then appended to the arguments we have already defined.</p>\n<pre><code class=\"language-javascript\"><span class=\"hljs-function\"><span class=\"hljs-keyword\">function</span> <span class=\"hljs-title\">greet</span> (<span class=\"hljs-params\">user, greeting</span>) </span>{\n  <span class=\"hljs-keyword\">return</span> greeting + <span class=\"hljs-string\">' '</span> + user;\n};\n\n<span class=\"hljs-keyword\">var</span> greetBlake = bind(greet, <span class=\"hljs-literal\">null</span>, <span class=\"hljs-string\">'Blake'</span>);\n\ngreetBlake(<span class=\"hljs-string\">'Hi'</span>); <span class=\"hljs-comment\">//=&gt; \"Hi Blake\"</span>\ngreetBlake(<span class=\"hljs-string\">'Hello'</span>); <span class=\"hljs-comment\">//=&gt; \"Hello Blake\"</span>\n</code></pre>\n<h2>Bonus Implementation using Variadic</h2>\n<p>In my last post, I introduced the concept of a <a href=\"../javascript-variadic-function\">variadic function</a>. As this article demonstrates, <code>bind</code> is a perfect example of a variadic function, so lets reimplement <code>bind</code> with the variadic function.</p>\n<pre><code class=\"language-javascript\"><span class=\"hljs-keyword\">var</span> bind = variadic(<span class=\"hljs-function\"><span class=\"hljs-keyword\">function</span> (<span class=\"hljs-params\">fn, context, args</span>) </span>{\n  <span class=\"hljs-keyword\">return</span> variadic(<span class=\"hljs-function\"><span class=\"hljs-keyword\">function</span> (<span class=\"hljs-params\">called</span>) </span>{\n    <span class=\"hljs-keyword\">return</span> fn.apply(context, args.concat(called));\n  });\n});\n</code></pre>\n"}

