	module.exports = {"title":"Composing Functions in JavaScript","date":"2014-01-19 00:00","author":"Blake Embrey","layout":"article","body":"<p>Composing multiple functions to create more complex ones is a common utility in any programming language. And the ability to construct functions in a way that is easily composable is a true talent, but it really shines with code maintenance and reuse. Its not uncommon to find huge applications composed of many, much smaller functions. Inspired by this pattern of extremely modular functions, Ive been slowly migrating my programming style to allow for more composable and reusable functions.</p>\n<p>To compose functions together, we will need to accept a list of functions for it to be made up from. Lets call the functions <code>a</code>, <code>b</code> and <code>c</code>. Now that we have the list of functions, we need to call each of them with the result of the next function. In JavaScript, we would do this with <code>a(b(c(x)))</code> - with <code>x</code> being the starting value. However, it would be much more useful to have something a little more reusable than this.</p>\n<pre><code class=\"language-javascript\"><span class=\"hljs-function\"><span class=\"hljs-keyword\">function</span> <span class=\"hljs-title\">compose</span> (<span class=\"hljs-params\"></span>) </span>{\n  <span class=\"hljs-keyword\">var</span> fns = <span class=\"hljs-built_in\">arguments</span>;\n\n  <span class=\"hljs-keyword\">return</span> <span class=\"hljs-function\"><span class=\"hljs-keyword\">function</span> (<span class=\"hljs-params\">result</span>) </span>{\n    <span class=\"hljs-keyword\">for</span> (<span class=\"hljs-keyword\">var</span> i = fns.length - <span class=\"hljs-number\">1</span>; i &gt; <span class=\"hljs-number\">-1</span>; i--) {\n      result = fns[i].call(<span class=\"hljs-keyword\">this</span>, result);\n    }\n\n    <span class=\"hljs-keyword\">return</span> result;\n  };\n};\n</code></pre>\n<p>The above function iterates over the function list (our arguments) in reverse - the last function to pass in is executed first. Given a single value as the initial input, itll chain that value between every function call and return the final result. This allows us to do some really cool things.</p>\n<pre><code class=\"language-javascript\"><span class=\"hljs-keyword\">var</span> number = compose(<span class=\"hljs-built_in\">Math</span>.round, <span class=\"hljs-built_in\">parseFloat</span>);\n\nnumber(<span class=\"hljs-string\">'72.5'</span>); <span class=\"hljs-comment\">//=&gt; 73</span>\n</code></pre>\n<h2>Sequence</h2>\n<p>Another utility Ive seen about in some functional libraries is called <a href=\"https://github.com/raganwald/allong.es#functional-composition\">sequence</a>. Its very similar to <code>compose</code>, except the arguments are executed in reverse. For example:</p>\n<pre><code class=\"language-javascript\"><span class=\"hljs-function\"><span class=\"hljs-keyword\">function</span> <span class=\"hljs-title\">sequence</span> (<span class=\"hljs-params\"></span>) </span>{\n  <span class=\"hljs-keyword\">var</span> fns = <span class=\"hljs-built_in\">arguments</span>;\n\n  <span class=\"hljs-keyword\">return</span> <span class=\"hljs-function\"><span class=\"hljs-keyword\">function</span> (<span class=\"hljs-params\">result</span>) </span>{\n    <span class=\"hljs-keyword\">for</span> (<span class=\"hljs-keyword\">var</span> i = <span class=\"hljs-number\">0</span>; i &lt; fns.length; i++) {\n      result = fns[i].call(<span class=\"hljs-keyword\">this</span>, result);\n    }\n\n    <span class=\"hljs-keyword\">return</span> result;\n  };\n};\n</code></pre>\n<p>However, we should make a note of the almost identical function signature to <code>compose</code>. Usually, seeing something like this should trigger a warning in your head to find some way to reuse previous functionality, instead of replicating it. In this example, we can reuse the <code>compose</code> function to write the <code>sequence</code> implementation.</p>\n<pre><code class=\"language-javascript\"><span class=\"hljs-keyword\">var</span> slice = <span class=\"hljs-built_in\">Array</span>.prototype.slice;\n\n<span class=\"hljs-keyword\">var</span> sequence = <span class=\"hljs-function\"><span class=\"hljs-keyword\">function</span> (<span class=\"hljs-params\"></span>) </span>{\n  <span class=\"hljs-keyword\">return</span> compose.apply(<span class=\"hljs-keyword\">this</span>, slice.call(<span class=\"hljs-built_in\">arguments</span>).reverse());\n};\n</code></pre>\n"}

