	module.exports = {"title":"Forcing Function Arity in JavaScript","date":"2014-01-22 23:30","author":"Blake Embrey","layout":"article","npm":"util-arity","github":"blakeembrey/arity","body":"<p>Function arity in something in JavaScript that is usually overlooked. For the most part, thats perfectly understandable, its just a number. Unfortunately, this number can be integral to many other functions working correctly. But first, what number am I talking about?</p>\n<pre><code class=\"language-javascript\"><span class=\"hljs-keyword\">var</span> fn = <span class=\"hljs-function\"><span class=\"hljs-keyword\">function</span> (<span class=\"hljs-params\">a, b</span>) </span>{};\n\nfn.length; <span class=\"hljs-comment\">//=&gt; 2</span>\n</code></pre>\n<p>As you can see, the length gives up the exact number of arguments the function is expecting to be passed in. This can be useful for other functions that might want to alter its behaviour based on this digit. For example, I found outlining this issue <a href=\"http://raganwald.com/2013/03/21/arity-and-partial-function-application.html\">with currying</a>. Basically, the <code>curry</code> function implementation relies on using the arity information to know how many times the function needs to be curried.</p>\n<p>To force the number of arity in our returned anonymous functions, we need to dynamically generate a function with the specified number of arguments. Why? Because the previous implementations of <a href=\"http://blakeembrey.com/articles/wrapping-javascript-functions/\">wrapping functions</a>, <a href=\"http://blakeembrey.com/articles/javascript-bind-function/\">bind</a>, <a href=\"http://blakeembrey.com/articles/javascript-variadic-function/\">variadic</a> and every other functional utility I have demonstrated dont proxy the number of arguments through the returned function.</p>\n<p>This can be a problem in the case where we want to use this function somewhere that expects a function length to work correctly, like when currying. We could fix this at the source, a half a dozen times and any number of times more. Or we could write a little utility that will enfore a number of arguments for us.</p>\n<pre><code class=\"language-javascript\"><span class=\"hljs-keyword\">var</span> names   = <span class=\"hljs-string\">'abcdefghijklmnopqrstuvwxyz'</span>;\n<span class=\"hljs-keyword\">var</span> __slice = <span class=\"hljs-built_in\">Array</span>.prototype.slice;\n\n<span class=\"hljs-comment\">/**\n * Make a function appear as though it accepts a certain number of arguments.\n *\n * @param  {Number}   length\n * @param  {Function} fn\n * @return {Function}\n */</span>\n<span class=\"hljs-keyword\">var</span> arity = <span class=\"hljs-function\"><span class=\"hljs-keyword\">function</span> (<span class=\"hljs-params\">length, fn</span>) </span>{\n  <span class=\"hljs-keyword\">return</span> <span class=\"hljs-built_in\">eval</span>(\n    <span class=\"hljs-string\">'(function ('</span> + __slice.call(names, <span class=\"hljs-number\">0</span>, length).join(<span class=\"hljs-string\">','</span>) + <span class=\"hljs-string\">') {\\n'</span> +\n    <span class=\"hljs-string\">'return fn.apply(this, arguments);\\n'</span> +\n    <span class=\"hljs-string\">'})'</span>\n  );\n};\n</code></pre>\n<p>The above function allows us to pass in an argument length and a function to proxy. It then returns to us an anonymous function with the correct number of arguments defined (<code>.length</code> works!) and allows us to call the function and return the usual result. It doesnt do anything to the arguments in the interim, it just tells the world how many arguments we are accepting.</p>\n<h2>The Other Arity Problem</h2>\n<p>So weve touched one of the arity problems, which is a expecting to read the correct arity from a function. The reverse arity problem is when a function is called with incorrect or overloaded arguments. Consider <code>parseInt</code>, which accepts two arguments - a string and the radix.</p>\n<pre><code class=\"language-javascript\">[<span class=\"hljs-number\">1</span>, <span class=\"hljs-number\">2</span>, <span class=\"hljs-number\">3</span>, <span class=\"hljs-number\">4</span>, <span class=\"hljs-number\">5</span>].map(<span class=\"hljs-built_in\">parseInt</span>); <span class=\"hljs-comment\">//=&gt; [1, NaN, NaN, NaN, NaN]</span>\n</code></pre>\n<p>Now were having problems. To fix this we can make a utility function that limits the number of arguments passed through.</p>\n<pre><code class=\"language-javascript\"><span class=\"hljs-keyword\">var</span> __slice = <span class=\"hljs-built_in\">Array</span>.prototype.slice;\n\n<span class=\"hljs-comment\">/**\n * Force a function to accept a specific number of arguments.\n *\n * @param  {Number}   length\n * @param  {Function} fn\n * @return {Function}\n */</span>\n<span class=\"hljs-keyword\">var</span> nary = <span class=\"hljs-function\"><span class=\"hljs-keyword\">function</span> (<span class=\"hljs-params\">length, fn</span>) </span>{\n  <span class=\"hljs-keyword\">return</span> <span class=\"hljs-function\"><span class=\"hljs-keyword\">function</span> (<span class=\"hljs-params\"></span>) </span>{\n    <span class=\"hljs-keyword\">return</span> fn.apply(<span class=\"hljs-keyword\">this</span>, __slice.call(<span class=\"hljs-built_in\">arguments</span>, <span class=\"hljs-number\">0</span>, length));\n  };\n};\n</code></pre>\n<p>If youve been reading, you would have just noticed that we introduced the original bug weve been trying to avoid. That is, were returning a new anonymous function without proxying the number of arguments through. Lets quickly correct that with the function we just wrote.</p>\n<pre><code class=\"language-javascript\"><span class=\"hljs-keyword\">var</span> __slice = <span class=\"hljs-built_in\">Array</span>.prototype.slice;\n\n<span class=\"hljs-comment\">/**\n * Force a function to accept a specific number of arguments.\n *\n * @param  {Number}   length\n * @param  {Function} fn\n * @return {Function}\n */</span>\n<span class=\"hljs-keyword\">var</span> nary = <span class=\"hljs-function\"><span class=\"hljs-keyword\">function</span> (<span class=\"hljs-params\">length, fn</span>) </span>{\n  <span class=\"hljs-comment\">// Uses the previous function to proxy the number of arguments.</span>\n  <span class=\"hljs-keyword\">return</span> arity(length, <span class=\"hljs-function\"><span class=\"hljs-keyword\">function</span> (<span class=\"hljs-params\"></span>) </span>{\n    <span class=\"hljs-keyword\">return</span> fn.apply(<span class=\"hljs-keyword\">this</span>, __slice.call(<span class=\"hljs-built_in\">arguments</span>, <span class=\"hljs-number\">0</span>, length));\n  });\n};\n</code></pre>\n<p>Now we can use this to fix our map error from earlier. We also have the added bonus of a correct argument representation.</p>\n<pre><code class=\"language-javascript\">nary(<span class=\"hljs-number\">1</span>, <span class=\"hljs-built_in\">parseInt</span>).length; <span class=\"hljs-comment\">//=&gt; 1</span>\n\n[<span class=\"hljs-number\">1</span>, <span class=\"hljs-number\">2</span>, <span class=\"hljs-number\">3</span>, <span class=\"hljs-number\">4</span>, <span class=\"hljs-number\">5</span>].map(nary(<span class=\"hljs-number\">1</span>, <span class=\"hljs-built_in\">parseInt</span>)); <span class=\"hljs-comment\">//=&gt; [1, 2, 3, 4, 5]</span>\n</code></pre>\n"}

