	module.exports = {"title":"A JavaScript Invoke Function","date":"2014-01-04 21:00","author":"Blake Embrey","layout":"article","body":"<p><strong>Update:</strong> Now available on <a href=\"https://github.com/blakeembrey/invoke\">github</a> and <a href=\"https://www.npmjs.org/package/util-invoke\">npm</a>.</p>\n<p>Under certain functional JavaScript toolbelts, we can find a utility that is used purely for invoking a method on a passed in object. The utility is a really simple snippet that can be used in a number of different circumstances.</p>\n<pre><code class=\"language-javascript\"><span class=\"hljs-keyword\">var</span> __slice = <span class=\"hljs-built_in\">Array</span>.prototype.slice;\n\n<span class=\"hljs-keyword\">var</span> invoke = <span class=\"hljs-function\"><span class=\"hljs-keyword\">function</span> (<span class=\"hljs-params\">method <span class=\"hljs-regexp\">/*, ...args */</span></span>) </span>{\n  <span class=\"hljs-keyword\">var</span> args = __slice.call(<span class=\"hljs-built_in\">arguments</span>, <span class=\"hljs-number\">1</span>);\n\n  <span class=\"hljs-keyword\">return</span> <span class=\"hljs-function\"><span class=\"hljs-keyword\">function</span> (<span class=\"hljs-params\">obj <span class=\"hljs-regexp\">/*, ..args */</span></span>) </span>{\n    <span class=\"hljs-keyword\">return</span> obj[method].apply(obj, args.concat(__slice.call(<span class=\"hljs-built_in\">arguments</span>, <span class=\"hljs-number\">1</span>)));\n  };\n};\n</code></pre>\n<p>The most useful situation for a utility such as this is in combination with other functional utilities and iterators. Consider the case where we have an array of objects with identical methods. Not uncommon in a complex MVC application where you may be tracking child views. To remove every child view, we need to iterate over an array of views and call <code>remove</code>.</p>\n<pre><code class=\"language-javascript\"><span class=\"hljs-keyword\">var</span> children = [<span class=\"hljs-comment\">/* ... */</span>];\n\nchildren.forEach(invoke(<span class=\"hljs-string\">'remove'</span>));\n</code></pre>\n"}

