	module.exports = {"title":"JavaScript Result Utility","date":"2014-01-09 17:00","author":"Blake Embrey","layout":"article","body":"<p>In JavaScript, an objects property can hold any type of data, from strings to functions and even nested objects. Since an object can hold any type of potential data, some libraries implement a <a href=\"http://underscorejs.org/#result\">result utility</a> to check whether the property is function and instead return the functions return.</p>\n<p>This functionality is rarely required in your regular JavaScript workflow and is usually reserved for third-party libraries where users can pass in ambiguous data types. A prominent example would be a templating library, where the template can render simple data types like strings and numbers. However, the template may also be capable of rendering ambiguous or dynamic data in the form of functions. To do this, we could abstract it to the following utility.</p>\n<pre><code class=\"language-javascript\"><span class=\"hljs-keyword\">var</span> result = <span class=\"hljs-function\"><span class=\"hljs-keyword\">function</span> (<span class=\"hljs-params\">obj, property</span>) </span>{\n  <span class=\"hljs-keyword\">var</span> value = obj[property];\n\n  <span class=\"hljs-keyword\">if</span> (<span class=\"hljs-keyword\">typeof</span> value === <span class=\"hljs-string\">'function'</span>) {\n    value = value.call(obj);\n  }\n\n  <span class=\"hljs-keyword\">return</span> value;\n};\n</code></pre>\n<p>The above is a relatively simple utility, but hopefully you can now imagine some use cases you can refactor and reuse a utility like this. However, consider a slightly different utility Im going call <code>ambiguous</code>. This utility is similar, albeit entirely incompatible, but works around some of the limitations I found when using the <code>result</code> function.</p>\n<pre><code class=\"language-javascript\"><span class=\"hljs-keyword\">var</span> __slice = <span class=\"hljs-built_in\">Array</span>.prototype.slice;\n\n<span class=\"hljs-keyword\">var</span> ambiguous = <span class=\"hljs-function\"><span class=\"hljs-keyword\">function</span> (<span class=\"hljs-params\">value <span class=\"hljs-regexp\">/*, ...args */</span></span>) </span>{\n  <span class=\"hljs-keyword\">if</span> (<span class=\"hljs-keyword\">typeof</span> value === <span class=\"hljs-string\">'function'</span>) {\n    value = value.apply(<span class=\"hljs-keyword\">this</span>, __slice.call(<span class=\"hljs-built_in\">arguments</span>, <span class=\"hljs-number\">1</span>));\n  }\n\n  <span class=\"hljs-keyword\">return</span> value;\n};\n</code></pre>\n<p>Personally, the <code>ambiguous</code> utility is much more useful in the real world. Especially since it removes the static context limitation from the <code>result</code> utility above. It also allows us to pass in optional arguments in the case that the value is a function. Just some food for thought.</p>\n"}

