	module.exports = {"title":"The Tap Utility in JavaScript","date":"2014-01-04 20:30","author":"Blake Embrey","layout":"article","body":"<p>There are numerous functional JavaScript libraries out in the public, many of which include a <a href=\"http://underscorejs.org/#tap\">utility called tap</a>. This simplistic utility serves a single purpose, to call a function with a value and return the value back to us. However, the usefulness of this utility is rarely understood and can even be confusing when looking at examples.</p>\n<pre><code class=\"language-javascript\"><span class=\"hljs-keyword\">var</span> tap = <span class=\"hljs-function\"><span class=\"hljs-keyword\">function</span> (<span class=\"hljs-params\">value, fn</span>) </span>{\n  fn(value);\n  <span class=\"hljs-keyword\">return</span> value;\n};\n</code></pre>\n<p>The code above depicts the simplicity of the function, but when would we ever use it? Its really only useful when we want to chain the value between calls. You can pass in any function and you will always receive the passed in value as the return, regardless of the function return value.</p>\n<pre><code class=\"language-javascript\"><span class=\"hljs-comment\">// Pop a value of the end of an array, in reality we'd use an `invoke` utility.</span>\ntap([<span class=\"hljs-number\">1</span>, <span class=\"hljs-number\">2</span>, <span class=\"hljs-number\">3</span>, <span class=\"hljs-number\">4</span>], <span class=\"hljs-function\"><span class=\"hljs-keyword\">function</span> (<span class=\"hljs-params\">array</span>) </span>{\n  <span class=\"hljs-comment\">// Pop always returns the value it removed from the end of the array.</span>\n  <span class=\"hljs-keyword\">return</span> array.pop();\n});\n<span class=\"hljs-comment\">//=&gt; [1, 2, 3]</span>\n</code></pre>\n"}

