	module.exports = {"title":"Partial Application in JavaScript","date":"2014-01-25 23:00","author":"Blake Embrey","layout":"article","body":"<p>Partial application is the act of pre-filling arguments of a function and returning a new function of smaller arity. The returned function can be called with additional parameters and in JavaScript, the <code>this</code> context can also be changed when called. Using a partially applied function is extremely common in functional programming with JavaScript as it allows us to compose some really nifty utilities and avoid repeating ourselves in code.</p>\n<p>In modern JavaScript engines, there is a <a href=\"https://developer.mozilla.org/en-US/docs/Web/JavaScript/Reference/Global_Objects/Function/bind\">bind function</a> which can be used to achieve a similar result. The difference between <code>partial</code> and <code>bind</code> is that the a partial functions <code>this</code> context is set when the returned function is called, while a bound functions <code>this</code> context has already been defined and cant be changed.</p>\n<pre><code class=\"language-javascript\"><span class=\"hljs-keyword\">var</span> __slice = <span class=\"hljs-built_in\">Array</span>.prototype.slice;\n\n<span class=\"hljs-comment\">/**\n * Wrap a function with default arguments for partial application.\n *\n * @param  {Function} fn\n * @param  {*}        ...\n * @return {Function}\n */</span>\n<span class=\"hljs-keyword\">var</span> partial = <span class=\"hljs-function\"><span class=\"hljs-keyword\">function</span> (<span class=\"hljs-params\">fn <span class=\"hljs-regexp\">/*, ...args */</span></span>) </span>{\n  <span class=\"hljs-keyword\">var</span> args = __slice.call(<span class=\"hljs-built_in\">arguments</span>, <span class=\"hljs-number\">1</span>);\n\n  <span class=\"hljs-keyword\">return</span> <span class=\"hljs-function\"><span class=\"hljs-keyword\">function</span> (<span class=\"hljs-params\"></span>) </span>{\n    <span class=\"hljs-keyword\">return</span> fn.apply(<span class=\"hljs-keyword\">this</span>, args.concat(__slice.call(<span class=\"hljs-built_in\">arguments</span>)));\n  };\n};\n</code></pre>\n<p>From the function above, we can understand that <code>partial</code> accepts the function to be pre-filled and its default arguments. It then returns a new function which can be called with some more arguments. Its important to note that the context (<code>this</code>) is being defined when the returned function is called. But when would you even want to use this?</p>\n<p>Normally I would be happy to give a simple example of transforming an <code>add</code> function into an <code>add5</code> by partially applying it - <code>partial(add, 5)</code>. This definitely demonstates how we can use the utility, but doesnt really touch on why.</p>\n<p>Consider writing a logging utility that accepts some different arguments that need to be logged - <code>var log = function (type, value) {}</code>. Fantastic, it looks like a really simple function to use. But now we want set every log in our file to the <code>testing</code> type. We can do a couple of things to achieve this. One option would be to assign our type to a variable and reuse the variable  - <code>var testType = 'Testing'</code> and <code>log(testType, value)</code>. This will get messy after we write it more than once. What if we just wrapped the <code>log</code> function automatically?</p>\n<pre><code class=\"language-javascript\"><span class=\"hljs-keyword\">var</span> testLog = <span class=\"hljs-function\"><span class=\"hljs-keyword\">function</span> (<span class=\"hljs-params\"></span>) </span>{\n  <span class=\"hljs-keyword\">return</span> log.apply(<span class=\"hljs-keyword\">this</span>, [<span class=\"hljs-string\">'testing'</span>].concat(__slice.call(<span class=\"hljs-built_in\">arguments</span>)));\n};\n</code></pre>\n<p>Great, this looks familiar - we could have just used partial - <code>var testLog = partial(log, 'Testing')</code>. Now we have a function we can continue to reuse any number of times without fear of repeating ourselves.</p>\n<h2>Bonus Points</h2>\n<p>If youve been reading any of my previous blog posts, you may have noticed me abusing the usefulness of <a href=\"http://blakeembrey.com/articles/forcing-function-arity-in-javascript/\">function arity</a> in anonymously returned functions. And in another article I wrote about a utility that can help us remove the <a href=\"http://blakeembrey.com/articles/javascript-variadic-function/\">repetitive argument slicing</a>. If you havent checked out these utilities yet, take a quick look and I bet youll see how we could use them here.</p>\n<pre><code class=\"language-javascript\"><span class=\"hljs-keyword\">var</span> partial = variadic(<span class=\"hljs-function\"><span class=\"hljs-keyword\">function</span> (<span class=\"hljs-params\">fn, args</span>) </span>{\n  <span class=\"hljs-keyword\">var</span> remaining = <span class=\"hljs-built_in\">Math</span>.max(fn.length - args.length, <span class=\"hljs-number\">0</span>);\n\n  <span class=\"hljs-keyword\">return</span> arity(remaining, variadic(<span class=\"hljs-function\"><span class=\"hljs-keyword\">function</span> (<span class=\"hljs-params\">called</span>) </span>{\n    <span class=\"hljs-keyword\">return</span> fn.apply(<span class=\"hljs-keyword\">this</span>, args.concat(called));\n  }));\n});\n</code></pre>\n<p>Now the returned partially applied function gives us the correct number of trailing arguments still to be filled using the <code>arity</code> utility. On top of that, we managed to get rid of slicing arguments constantly by using the <code>variadic</code> utility. In fact, Ive been so interested in these reusable utilities that I published the <a href=\"https://github.com/blakeembrey/partial\">partial utility on Github</a> so I can reuse it later.</p>\n"}

