	module.exports = {"title":"Enabling Generators in Mocha","date":"2014-02-03 12:30","author":"Blake Embrey","layout":"article","npm":"co-mocha","github":"blakeembrey/co-mocha","body":"<p><strong>Update:</strong> Its on <a href=\"http://npmjs.org/package/co-mocha\">npm</a>. Just <code>npm install co-mocha</code> and <code>--require co-mocha</code> in your mocha script to test using generators.</p>\n<p>For the last week, Ive been working on a new project that uses node 0.11 and generators. Aside from the numerous benefits with generators in the application, I also integrated generators into my test suite. By using <a href=\"http://visionmedia.github.io/mocha/\">mocha</a> and <a href=\"https://github.com/visionmedia/co\">co</a>, you can also enable support for generator functions in your test suite.</p>\n<pre><code class=\"language-javascript\"><span class=\"hljs-keyword\">var</span> co       = <span class=\"hljs-built_in\">require</span>(<span class=\"hljs-string\">'co'</span>);\n<span class=\"hljs-keyword\">var</span> mocha    = <span class=\"hljs-built_in\">require</span>(<span class=\"hljs-string\">'mocha'</span>);\n<span class=\"hljs-keyword\">var</span> Runnable = mocha.Runnable;\n<span class=\"hljs-keyword\">var</span> run      = Runnable.prototype.run;\n\n<span class=\"hljs-comment\">/**\n * Override the Mocha function runner and enable generator support with co.\n *\n * @param {Function} fn\n */</span>\nRunnable.prototype.run = <span class=\"hljs-function\"><span class=\"hljs-keyword\">function</span> (<span class=\"hljs-params\">fn</span>) </span>{\n  <span class=\"hljs-keyword\">if</span> (<span class=\"hljs-keyword\">this</span>.fn.constructor.name === <span class=\"hljs-string\">'GeneratorFunction'</span>) {\n    <span class=\"hljs-keyword\">this</span>.fn   = co(<span class=\"hljs-keyword\">this</span>.fn);\n    <span class=\"hljs-keyword\">this</span>.sync = !(<span class=\"hljs-keyword\">this</span>.async = <span class=\"hljs-literal\">true</span>);\n  }\n\n  <span class=\"hljs-keyword\">return</span> run.call(<span class=\"hljs-keyword\">this</span>, fn);\n};\n</code></pre>\n<p>Save the snippet of code above into a JavaScript file and pass the filename to <code>mocha.opts</code>.</p>\n<pre><code>mocha --harmony-generators --require test/support/co-mocha\n</code></pre>\n<p>Writing tests using generators has made testing even more enjoyable, so I decided to release the <a href=\"https://github.com/blakeembrey/co-mocha\">code on Github</a> with some accompanying tests and test coverage.</p>\n"}

