	module.exports = {"title":"Avoid type=\"number\" in AngularJS","date":"2014-05-09 20:00","author":"Blake Embrey","layout":"article","body":"<p>The other day I got hit by a peculiar bug in Angular. Using <code>type=&quot;number&quot;</code> on an input element wouldnt do any number validation. On top of this, when I entered an invalid number the only validation failing was <code>required</code>.</p>\n<p>After a little research, it turned out to be a <a href=\"http://www.w3.org/TR/html5/forms.html#number-state-%28type=number%29\">feature</a> of blocking access to the <code>value</code> attribute when its an invalid number. Not all browsers follow the complete spec, so I found this was working in Firefox. Back in Chrome however, it was failing. You can even test this in the <a href=\"https://docs.angularjs.org/api/ng/input/input%5Bnumber%5D\">AngularJS documentation</a> by typing an invalid number in the demo and looking at the error message.</p>\n<h2>Custom Type Directive</h2>\n<pre><code class=\"language-javascript\"><span class=\"hljs-keyword\">var</span> app = angular.module(<span class=\"hljs-string\">'myApp'</span>, []);\n\n<span class=\"hljs-comment\">/**\n * Provide custom type validation for input elements. Certain type attributes\n * don't work consistenty cross-browser, so this is a required workaround.\n * Looking at you, webkit and `type=\"number\"`.\n *\n * ```html\n * &lt;input\n *   ng-model=\"\"\n *   app-type=\"\"&gt;\n * ```\n */</span>\napp.directive(<span class=\"hljs-string\">'appType'</span>, <span class=\"hljs-function\"><span class=\"hljs-keyword\">function</span> (<span class=\"hljs-params\"></span>) </span>{\n  <span class=\"hljs-keyword\">return</span> {\n    <span class=\"hljs-attr\">require</span>: <span class=\"hljs-string\">'ngModel'</span>,\n    <span class=\"hljs-attr\">link</span>: <span class=\"hljs-function\"><span class=\"hljs-keyword\">function</span> (<span class=\"hljs-params\">scope, elem, attrs, ctrl</span>) </span>{\n      <span class=\"hljs-comment\">// Custom number validation logic.</span>\n      <span class=\"hljs-keyword\">if</span> (attrs.appType === <span class=\"hljs-string\">'number'</span>) {\n        <span class=\"hljs-keyword\">return</span> ctrl.$parsers.push(<span class=\"hljs-function\"><span class=\"hljs-keyword\">function</span> (<span class=\"hljs-params\">value</span>) </span>{\n          <span class=\"hljs-keyword\">var</span> valid = value == <span class=\"hljs-literal\">null</span> || <span class=\"hljs-built_in\">isFinite</span>(value);\n\n          ctrl.$setValidity(<span class=\"hljs-string\">'number'</span>, valid);\n\n          <span class=\"hljs-keyword\">return</span> valid &amp;&amp; value != <span class=\"hljs-literal\">null</span> ? <span class=\"hljs-built_in\">Number</span>(value) : <span class=\"hljs-literal\">undefined</span>;\n        });\n      }\n    }\n  };\n});\n</code></pre>\n<p>The code above adds a new custom directive that requires <code>ngModel</code>. Requiring <code>ngModel</code> provides us with the <a href=\"http://docs.angularjs.org/api/ng.directive:ngModel.NgModelController\">ngModelController</a>. Using the controller we can access some useful methods, including model input parsing and validity - which makes up the bulk of our validation logic.</p>\n<p>When the type is <code>number</code>, we push a custom parser onto the stack. Our parser goes on the end and will run after any other parsers, allowing us to keep the <code>required</code> directive in tact. The validity itself checks if the value is empty (<code>null</code> or <code>undefined</code>) or that its a valid JavaScript number. Valid JavaScript number is important to note, since this will allow the most comprehensive check including integers, floats, negative and positive notation, but also other notations such as <code>0x1e5</code> and <code>1e5</code>.</p>\n<p>Validity gets set next and based on the result well coerce the value into a number. By doing number coercion, the model will correctly receive the number instead of the string representation. We want to avoid coercing empty values (<code>null</code> and <code>undefined</code>) however, which will come out as <code>0</code> and <code>NaN</code> respectfully.</p>\n<h2>Recreating min and max directives</h2>\n<pre><code class=\"language-javascript\"><span class=\"hljs-keyword\">var</span> app = angular.module(<span class=\"hljs-string\">'myApp'</span>, []);\n\n<span class=\"hljs-comment\">/**\n * Provide minimum number validation for any input.\n *\n * ```html\n * &lt;input\n *   ng-model=\"\"\n *   app-min=\"\"&gt;\n * ```\n */</span>\napp.directive(<span class=\"hljs-string\">'appMin'</span>, <span class=\"hljs-function\"><span class=\"hljs-keyword\">function</span> (<span class=\"hljs-params\"></span>) </span>{\n  <span class=\"hljs-keyword\">return</span> {\n    <span class=\"hljs-attr\">require</span>: <span class=\"hljs-string\">'ngModel'</span>,\n    <span class=\"hljs-attr\">link</span>: <span class=\"hljs-function\"><span class=\"hljs-keyword\">function</span> (<span class=\"hljs-params\">scope, elem, attrs, ctrl</span>) </span>{\n      <span class=\"hljs-keyword\">return</span> ctrl.$parsers.push(<span class=\"hljs-function\"><span class=\"hljs-keyword\">function</span> (<span class=\"hljs-params\">value</span>) </span>{\n        <span class=\"hljs-keyword\">var</span> valid = value == <span class=\"hljs-literal\">null</span> || <span class=\"hljs-built_in\">Number</span>(value) &gt;= <span class=\"hljs-built_in\">Number</span>(attrs.appMin);\n\n        ctrl.$setValidity(<span class=\"hljs-string\">'min'</span>, valid);\n\n        <span class=\"hljs-keyword\">return</span> valid ? value : <span class=\"hljs-literal\">undefined</span>;\n      });\n    }\n  };\n});\n</code></pre>\n<p>Recreating the <code>min</code> directive is trivial and we can easily make it work for any input. The above validation does the <code>null</code> check again, which will make an empty input element valid. This is important since we dont want to provide unnecessary validation and bundle the <code>required</code> directives job into ours.</p>\n<p>Next its just a process of coercing both the value and attribute into numbers and comparing the values. If either are <code>NaN</code>, validation will fail. This provides some form of ensuring we have numbers only, but wont do any number coerce to the model. Finally, we return either the value or <code>undefined</code> if validation failed.</p>\n<h2>Unit Testing</h2>\n<p>Unit testing the functionality was straightforward enough, so I wont provide all the code used. A couple of things worth mentioning though is how to compile the templates for testing and set the values for validation.</p>\n<pre><code class=\"language-javascript\">it(<span class=\"hljs-string\">'...'</span>, <span class=\"hljs-function\"><span class=\"hljs-keyword\">function</span> (<span class=\"hljs-params\"></span>) </span>{\n  inject(<span class=\"hljs-function\"><span class=\"hljs-keyword\">function</span> (<span class=\"hljs-params\">$compile, $rootScope</span>) </span>{\n    <span class=\"hljs-keyword\">var</span> $scope = $rootScope.$<span class=\"hljs-keyword\">new</span>();\n\n    $scope.model = {};\n\n    <span class=\"hljs-keyword\">var</span> $element = $compile(\n      <span class=\"hljs-string\">'&lt;form name=\"form\"&gt;'</span> +\n      <span class=\"hljs-string\">'  &lt;input name=\"num\" ng-model=\"model.value\" app-type=\"number\"&gt;'</span> +\n      <span class=\"hljs-string\">'&lt;/form&gt;'</span>\n    )($scope);\n\n    <span class=\"hljs-comment\">// Set the value to what you want to test.</span>\n    $scope.form.num.$setViewValue(<span class=\"hljs-string\">'10'</span>);\n\n    <span class=\"hljs-comment\">// Check the model is what you expect and check validation.</span>\n    $scope.model.value.should.equal(<span class=\"hljs-number\">10</span>);\n    $scope.form.num.$invalid.should.be.false;\n  });\n});\n</code></pre>\n"}

