	module.exports = {"title":"Building a Blog with Metalsmith","date":"2014-10-04 13:15","author":"Blake Embrey","layout":"article","body":"<p><a href=\"http://www.metalsmith.io/\">Metalsmith</a> is a new addition to static site generator space, only released at the beginning of this year. Its written in JavaScript and provides an extremely simple plugin system for composing files using middleware. The fact that everything is a plugin makes it incredibly easy to understand what is happening under the hood and extend it when you need to add new functionality.</p>\n<h2>Getting Started</h2>\n<p>For this tutorial, I converted the blog youre currently reading to Metalsmith. To start, well create a new project directory, then create a <code>src</code> directory inside of that. The first thing we need to save the snippet below into a <code>package.json</code>, which holds all our blog dependencies.</p>\n<pre><code class=\"language-json\">{\n  <span class=\"hljs-attr\">\"name\"</span>: <span class=\"hljs-string\">\"example-blog\"</span>,\n  <span class=\"hljs-attr\">\"version\"</span>: <span class=\"hljs-string\">\"0.0.0\"</span>,\n  <span class=\"hljs-attr\">\"private\"</span>: <span class=\"hljs-literal\">true</span>,\n  <span class=\"hljs-attr\">\"description\"</span>: <span class=\"hljs-string\">\"Example blog.\"</span>,\n  <span class=\"hljs-attr\">\"author\"</span>: <span class=\"hljs-string\">\"Blake Embrey\"</span>,\n  <span class=\"hljs-attr\">\"license\"</span>: <span class=\"hljs-string\">\"MIT\"</span>\n}\n</code></pre>\n<p>Now that you have a valid <code>package.json</code>, you will need to install <code>metalsmith</code> locally to get started. Execute <code>npm install metalsmith --save</code> and youll notice its been automatically added to the <code>package.json</code>. With that as the initial dependency, you need to create a <code>build.js</code> file with the content below.</p>\n<pre><code class=\"language-javascript\"><span class=\"hljs-keyword\">var</span> metalsmith = <span class=\"hljs-built_in\">require</span>(<span class=\"hljs-string\">'metalsmith'</span>);\n\nmetalsmith(__dirname)\n  .source(<span class=\"hljs-string\">'src'</span>)\n  .destination(<span class=\"hljs-string\">'build'</span>)\n  .build(<span class=\"hljs-function\"><span class=\"hljs-keyword\">function</span> (<span class=\"hljs-params\">err</span>) </span>{\n    <span class=\"hljs-keyword\">if</span> (err) {\n      <span class=\"hljs-keyword\">throw</span> err;\n    }\n  });\n</code></pre>\n<h2>Adding articles</h2>\n<p>Now you have a working Metalsmith instance - it just doesnt do anything yet. There arent any files in <code>src</code> and no plugins are being used to process data yet. First things first, lets create an <code>articles</code> (or <code>posts</code>, etc.) directory under <code>src</code>. This structure will a part of our url (E.g. <code>/articles/...</code>). Inside the directory you created, you need to create your first markdown file. The snippet below is a very simple article with metadata.</p>\n<pre><code>---\ntitle: Example title.\ndate: 2014-09-29 23:00\nauthor: Blake Embrey\ntemplate: article.jade\n---\n\nExample content.\n</code></pre>\n<p>Nothing exciting so far. If you build the blog by executing <code>node build.js</code>, youll see a new <code>build</code> directory with the contents copied from the <code>src</code> directory. However, if you look at your markdown article youll notice the metadata is missing. That is because the parts between the three dashes at the beginning is YAML font-matter (a type of associated metadata). We can access metadata from within any plugin or template, and it can be added to the beginning of any file and automatically be parsed.</p>\n<h2>Processing Markdown</h2>\n<p>The next step would be to add markdown processing into our build script. This will allow us to turn the all our markdown into HTML documents. To do this, well install the <a href=\"https://github.com/segmentio/metalsmith-markdown\">metalsmith-markdown</a> (using <code>npm install metalsmith-markdown --save</code>). If we also want syntax highlighting of code snippets, we can use <code>highlight.js</code> (or <code>highlighter</code> which offers a simple abstraction for markdown - <code>npm install highlighter --save</code>).</p>\n<pre><code class=\"language-javascript.diff\"><span class=\"diff-null\"><span class=\"hljs-keyword\">var</span> metalsmith  = <span class=\"hljs-built_in\">require</span>(<span class=\"hljs-string\">'metalsmith'</span>);</span>\n<span class=\"diff-addition\"><span class=\"hljs-keyword\">var</span> markdown    = <span class=\"hljs-built_in\">require</span>(<span class=\"hljs-string\">'metalsmith-markdown'</span>);\n<span class=\"hljs-keyword\">var</span> highlighter = <span class=\"hljs-built_in\">require</span>(<span class=\"hljs-string\">'highlighter'</span>);</span>\n<span class=\"diff-null\">\nmetalsmith(__dirname)\n  .source(<span class=\"hljs-string\">'src'</span>)</span>\n<span class=\"diff-addition\">  .use(markdown({\n    <span class=\"hljs-attr\">gfm</span>: <span class=\"hljs-literal\">true</span>,\n    <span class=\"hljs-attr\">tables</span>: <span class=\"hljs-literal\">true</span>,\n    <span class=\"hljs-attr\">highlight</span>: highlighter()\n  }))</span>\n<span class=\"diff-null\">  .destination(<span class=\"hljs-string\">'build'</span>)\n</span></code></pre>\n<p>Running your build script (<code>node build.js</code>) will now result in your markdown file becoming HTML. If you add a code block fenced by three backticks, youll also get to see syntax highlighting automatically applied (in HTML, still need to add the CSS - consider starting with a theme from the <a href=\"https://highlightjs.org/static/demo/\">highlight.js demo</a>).</p>\n<h2>Templates</h2>\n<p>Now its time to start making things look pretty by adding templates. Lets start out with the article template, since you already have an article to work with. Start by installing <a href=\"https://github.com/segmentio/metalsmith-templates\">metalsmith-templates</a> (<code>npm install metalsmith-templates --save</code>) and creating a <code>templates</code> directory. Create your first template file inside the templates directory. Since Im using <a href=\"https://github.com/visionmedia/jade\">jade</a> Ive created an <code>article.jade</code> template. Heres a simple example in Jade:</p>\n<pre><code class=\"language-jade\">html(lang='en')\n  head\n    meta(charset='utf-8')\n    meta(http-equiv='X-UA-Compatible', content='IE=edge,chrome=1')\n    meta(name='viewport', content='width=device-width')\n    title= title\n  body\n    h1= title\n    div.timestamp= date\n    article!= contents\n</code></pre>\n<p>With the template created, you need to include the plugin. And since Im using jade, Ill need to install <code>jade</code> using <code>npm install jade --save</code> (for Handlebars use <code>npm install handlebars --save</code> and change the engine below). Lets update our <code>build.js</code> with the code below.</p>\n<pre><code class=\"language-javascript.diff\"><span class=\"diff-null\"><span class=\"hljs-keyword\">var</span> metalsmith = <span class=\"hljs-built_in\">require</span>(<span class=\"hljs-string\">'metalsmith'</span>);\n<span class=\"hljs-keyword\">var</span> markdown   = <span class=\"hljs-built_in\">require</span>(<span class=\"hljs-string\">'metalsmith-markdown'</span>);\n<span class=\"hljs-keyword\">var</span> highlight  = <span class=\"hljs-built_in\">require</span>(<span class=\"hljs-string\">'highlight.js'</span>);</span>\n<span class=\"diff-addition\"><span class=\"hljs-keyword\">var</span> templates  = <span class=\"hljs-built_in\">require</span>(<span class=\"hljs-string\">'metalsmith-templates'</span>);</span>\n<span class=\"diff-null\">\nmetalsmith(__dirname)</span>\n<span class=\"diff-chunk\">@@ ... @@</span>\n<span class=\"diff-addition\">  .use(templates({\n    <span class=\"hljs-attr\">engine</span>: <span class=\"hljs-string\">'jade'</span>,\n    <span class=\"hljs-attr\">directory</span>: <span class=\"hljs-string\">'templates'</span>\n  }))</span>\n<span class=\"diff-null\">  .destination(<span class=\"hljs-string\">'build'</span>)\n</span></code></pre>\n<p>Running <code>node build.js</code> again will give you a complete HTML file in place of our markdown file. However, if youre using a different template file you will need to update the markdown metadata. In the template file you have access to the variables such as <code>contents</code>, <code>date</code> and <code>title</code> without having to define them. This is because the metadata is coming from our inline definition and <code>contents</code> is our files content. All plugins can interact, manipulate and add to this data.</p>\n<h2>Pretty Permalinks</h2>\n<p>You can add permalinks to your blog by using <a href=\"https://github.com/segmentio/metalsmith-permalinks\">metalsmith-permalinks</a> (<code>npm install metalsmith-permalinks --save</code>). Although you have the option for setting a pattern to rewrite URLs, I opted to use it without any options set. This is so itll just fix up all my paths to look cleaner anyway, and add the fancy <code>path</code> property to the files metadata.</p>\n<pre><code class=\"language-javascript.diff\"><span class=\"diff-chunk\">@@ ... @@</span>\n<span class=\"diff-null\"><span class=\"hljs-keyword\">var</span> highlight  = <span class=\"hljs-built_in\">require</span>(<span class=\"hljs-string\">'highlight.js'</span>);\n<span class=\"hljs-keyword\">var</span> templates  = <span class=\"hljs-built_in\">require</span>(<span class=\"hljs-string\">'metalsmith-templates'</span>);</span>\n<span class=\"diff-addition\"><span class=\"hljs-keyword\">var</span> permalinks = <span class=\"hljs-built_in\">require</span>(<span class=\"hljs-string\">'metalsmith-permalinks'</span>);</span>\n<span class=\"diff-null\">\nmetalsmith(__dirname)</span>\n<span class=\"diff-chunk\">@@ ... @@</span>\n<span class=\"diff-addition\">  .use(permalinks())</span>\n<span class=\"diff-null\">  .use(templates({\n    <span class=\"hljs-attr\">engine</span>: <span class=\"hljs-string\">'jade'</span>,\n    <span class=\"hljs-attr\">directory</span>: <span class=\"hljs-string\">'templates'</span>\n  }))\n  .destination(<span class=\"hljs-string\">'build'</span>)\n</span></code></pre>\n<h2>Collections</h2>\n<p>Perfect, so far weve got nice fancy URLs and blog posts rendering. Next well want to group all the articles together into a collection for rendering on the homepage. This also links the posts between each other so we can do things like next and previous links. As youve probably realised, you can install a plugin for this purpose - <a href=\"https://github.com/segmentio/metalsmith-collections\">metalsmith-collections</a> using <code>npm install metalsmith-collections --save</code>. Once installed, add it to the <code>build.js</code>.</p>\n<pre><code class=\"language-javascript.diff\"><span class=\"diff-chunk\">@@ ... @@</span>\n<span class=\"diff-null\"><span class=\"hljs-keyword\">var</span> permalinks  = <span class=\"hljs-built_in\">require</span>(<span class=\"hljs-string\">'metalsmith-permalinks'</span>);</span>\n<span class=\"diff-addition\"><span class=\"hljs-keyword\">var</span> collections = <span class=\"hljs-built_in\">require</span>(<span class=\"hljs-string\">'metalsmith-collections'</span>);</span>\n<span class=\"diff-null\">\nmetalsmith(__dirname)\n  .source(<span class=\"hljs-string\">'src'</span>)</span>\n<span class=\"diff-addition\">  .use(collections({\n    <span class=\"hljs-attr\">articles</span>: {\n      <span class=\"hljs-attr\">pattern</span>: <span class=\"hljs-string\">'articles/**/*.md'</span>,\n      <span class=\"hljs-attr\">sortBy</span>: <span class=\"hljs-string\">'date'</span>,\n      <span class=\"hljs-attr\">reverse</span>: <span class=\"hljs-literal\">true</span>\n    }\n  }))</span>\n<span class=\"diff-chunk\">@@ ... @@</span>\n<span class=\"diff-null\"></span></code></pre>\n<p>The snippet above will match all markdown files in the <code>articles</code> directory, then sort them in chronological order with the most recent one at the beginning - just like a blog. The collection itself is stored as global Metalsmith metadata under <code>collections.articles</code>. Just make sure you add the collections plugin before our templates and markdown plugins since they always run in order.</p>\n<h2>Homepage</h2>\n<p>Now that we have a collection of all our posts, lets create a homepage. Using your template engine of choice, you can add a new template file to the <code>templates</code> directory. For example, Im creating <code>index.jade</code> like below.</p>\n<pre><code class=\"language-jade\">html(lang='en')\n  head\n    meta(charset='utf-8')\n    meta(http-equiv='X-UA-Compatible', content='IE=edge,chrome=1')\n    meta(name='viewport', content='width=device-width')\n    title= 'Home'\n  body\n    each article in collections.articles\n      article.content-article\n        header\n          span.timestamp= article.date\n          h2\n            a(href='/' + article.path)= article.title\n</code></pre>\n<p>Now were talking! Lets render this template by creating an <code>index.html</code> file in the root of our <code>src</code> directory. This will simply act as metadata for rendering our template, as below.</p>\n<pre><code>---\ntemplate: index.jade\n---\n</code></pre>\n<p>With that complete, you can run the build again (<code>node build</code>) and stare in awe at your beautiful homepage. At this point you might want to start serving the <code>build</code> folder locally too, so I run <code>npm install -g serve</code> and then used <code>serve build</code> to view the directory.</p>\n<h2>Adding Global Metadata</h2>\n<p>You may have noticed that itd be handy to add some generic metadata or modules to reuse in templates. I hear you, so you can install <a href=\"https://github.com/aymericbeaumet/metalsmith-define\">metalsmith-define</a> (<code>npm install metalsmith-define</code>) for this. With that installed, you can now define global metadata anywhere in the metalsmith middleware stack.</p>\n<pre><code class=\"language-javascript.diff\"><span class=\"diff-chunk\">@@ ... @@</span>\n<span class=\"diff-null\"><span class=\"hljs-keyword\">var</span> collections = <span class=\"hljs-built_in\">require</span>(<span class=\"hljs-string\">'metalsmith-collections'</span>);</span>\n<span class=\"diff-addition\"><span class=\"hljs-keyword\">var</span> define      = <span class=\"hljs-built_in\">require</span>(<span class=\"hljs-string\">'metalsmith-define'</span>);</span>\n<span class=\"diff-null\">\nmetalsmith(__dirname)\n  .source(<span class=\"hljs-string\">'src'</span>)</span>\n<span class=\"diff-addition\">  .use(define({\n    <span class=\"hljs-attr\">blog</span>: {\n      <span class=\"hljs-attr\">url</span>: <span class=\"hljs-string\">'http://blakeembrey.com'</span>,\n      <span class=\"hljs-attr\">title</span>: <span class=\"hljs-string\">'Blake Embrey'</span>,\n      <span class=\"hljs-attr\">description</span>: <span class=\"hljs-string\">'Hello world.'</span>\n    },\n    <span class=\"hljs-attr\">owner</span>: {\n      <span class=\"hljs-attr\">url</span>: <span class=\"hljs-string\">'http://blakeembrey.me'</span>,\n      <span class=\"hljs-attr\">name</span>: <span class=\"hljs-string\">'Blake Embrey'</span>\n    },\n    <span class=\"hljs-attr\">moment</span>: <span class=\"hljs-built_in\">require</span>(<span class=\"hljs-string\">'moment'</span>)\n  }))</span>\n<span class=\"diff-null\">  .use(collections({\n    <span class=\"hljs-attr\">articles</span>: {\n      <span class=\"hljs-attr\">pattern</span>: <span class=\"hljs-string\">'articles/**/*.md'</span>,\n      <span class=\"hljs-attr\">sortBy</span>: <span class=\"hljs-string\">'date'</span>,\n      <span class=\"hljs-attr\">reverse</span>: <span class=\"hljs-literal\">true</span>\n    }\n  }))</span>\n<span class=\"diff-chunk\">@@ ... @@</span>\n<span class=\"diff-null\"></span></code></pre>\n<p>With the above, I have defined some information about the blog and the owner. I also added <a href=\"http://momentjs.com/\">moment</a> (<code>npm install moment --save</code>) because itll be handy for formatting our dates. I might quickly go back to our article and homepages templates now and wrap our date in <code>moment(date).format('MMMM D, YYYY')</code> for prettier dates. In Handlebars, you can create a <code>formatDate</code> helper and use moment that way.</p>\n<h2>Pagination</h2>\n<p>Wow, were nearly done already! Lets add the one thing every good blog deserves, pagination. To get started, install <a href=\"https://github.com/blakeembrey/metalsmith-pagination\">metalsmith-pagination</a> (<code>npm install metalsmith-pagination</code>), it works with any array structure and integrates with templates. We can now delete <code>index.md</code> from the <code>src</code> directory as the plugin will generate it for us (using <code>first: 'index.html</code>).</p>\n<pre><code class=\"language-javascript.diff\"><span class=\"diff-chunk\">@@ ... @@</span>\n<span class=\"diff-null\"><span class=\"hljs-keyword\">var</span> define      = <span class=\"hljs-built_in\">require</span>(<span class=\"hljs-string\">'metalsmith-define'</span>);</span>\n<span class=\"diff-addition\"><span class=\"hljs-keyword\">var</span> pagination  = <span class=\"hljs-built_in\">require</span>(<span class=\"hljs-string\">'metalsmith-pagination'</span>);</span>\n<span class=\"diff-null\"></span>\n<span class=\"diff-chunk\">@@ ... @@</span>\n<span class=\"diff-null\">  .use(collections({\n    <span class=\"hljs-attr\">articles</span>: {\n      <span class=\"hljs-attr\">pattern</span>: <span class=\"hljs-string\">'articles/**/*.md'</span>,\n      <span class=\"hljs-attr\">sortBy</span>: <span class=\"hljs-string\">'date'</span>,\n      <span class=\"hljs-attr\">reverse</span>: <span class=\"hljs-literal\">true</span>\n    }\n  }))</span>\n<span class=\"diff-addition\">  .use(pagination({\n    <span class=\"hljs-string\">'collections.articles'</span>: {\n      <span class=\"hljs-attr\">perPage</span>: <span class=\"hljs-number\">5</span>,\n      <span class=\"hljs-attr\">first</span>: <span class=\"hljs-string\">'index.html'</span>,\n      <span class=\"hljs-attr\">path</span>: <span class=\"hljs-string\">'page/:num/index.html'</span>,\n      <span class=\"hljs-attr\">template</span>: <span class=\"hljs-string\">'index.jade'</span>\n    }\n  }))</span>\n<span class=\"diff-chunk\">@@ ... @@</span>\n<span class=\"diff-null\"></span></code></pre>\n<p>We can set a limit of files per page, the path to all pages, an alternative first page location, and the template for rendering the files. With this defined, youll need to make some tweaks and improvements to the <code>index.jade</code> template.</p>\n<pre><code class=\"language-jade.diff\"><span class=\"diff-null\">html(lang='en')\n  head\n    meta(charset='utf-8')\n    meta(http-equiv='X-UA-Compatible', content='IE=edge,chrome=1')\n    meta(name='viewport', content='width=device-width')\n    title= 'Home'\n  body</span>\n<span class=\"diff-deletion\">    each article in collections.articles</span>\n<span class=\"diff-addition\">    each article in pagination.files</span>\n<span class=\"diff-null\">      article.content-article\n        header\n          span.timestamp= moment(article.date).format('MMMM D, YYYY')\n          h2\n            a(href='/' + article.path)= article.title</span>\n<span class=\"diff-addition\">  nav\n    if pagination.previous\n      a.btn(href='/' + pagination.previous.path)\n        | Newer\n    if pagination.next\n      a.btn(href='/' + pagination.next.path)\n        | Older</span>\n<span class=\"diff-null\"></span></code></pre>\n<p>Notice that the loop has changed to iterate over <code>pagination.files</code> instead. Also next and previous buttons have been added to the homepage. For the plugin to work properly, it needs to be included after the collections plugin, but before the permalinks and templates plugins. This works because we need access to the collections metadata in the plugin, but need the generated files to have a path and content created using templates.</p>\n<h2>Article Snippets</h2>\n<p>Another thing that all good blogs seem to have are content snippets. For this, youll install <a href=\"https://github.com/blakeembrey/metalsmith-snippet\">metalsmith-snippet</a> which allows you to access a short snippet of the HTML files in templates. Lets include the plugin in the <code>build.js</code> file.</p>\n<pre><code class=\"language-javascript.diff\"><span class=\"diff-chunk\">@@ ... @@</span>\n<span class=\"diff-null\"><span class=\"hljs-keyword\">var</span> paginate    = <span class=\"hljs-built_in\">require</span>(<span class=\"hljs-string\">'metalsmith-collections-paginate'</span>);</span>\n<span class=\"diff-addition\"><span class=\"hljs-keyword\">var</span> snippet     = <span class=\"hljs-built_in\">require</span>(<span class=\"hljs-string\">'metalsmith-snippet'</span>);</span>\n<span class=\"diff-null\"></span>\n<span class=\"diff-chunk\">@@ ... @@</span>\n<span class=\"diff-addition\">  .use(snippet())</span>\n<span class=\"diff-null\">  .use(permalinks())\n  .use(templates({\n    <span class=\"hljs-attr\">engine</span>: <span class=\"hljs-string\">'jade'</span>,\n    <span class=\"hljs-attr\">directory</span>: <span class=\"hljs-string\">'templates'</span>\n  }))</span>\n<span class=\"diff-chunk\">@@ ... @@</span>\n<span class=\"diff-null\"></span></code></pre>\n<p>This will automatically generate a snippet for all the articles based on a number of characters. Make sure it comes after the markdown parsing is done though. With this enabled, we can add <code>article.snippet</code> to the homepage template and print out article summaries.</p>\n<h2>Enable XML Feed</h2>\n<p>Next up, we will enable an XML feed for the blog. This will allow people to subscribe to the blog for new articles in feed readers. To do this, youll need to create a template for our feed. Heres my <code>feed.jade</code> template:</p>\n<pre><code class=\"language-jade\">doctype xml\nrss(version='2.0',\n    xmlns:content='http://purl.org/rss/1.0/modules/content/',\n    xmlns:wfw='http://wellformedweb.org/CommentAPI/',\n    xmlns:dc='http://purl.org/dc/elements/1.1/'\n    xmlns:atom='http://www.w3.org/2005/Atom')\n  channel\n    title= blog.title\n    atom:link(href=blog.url + '/feed.xml', rel='self', type='application/rss+xml')\n    link= blog.url\n    description= blog.description\n    pubDate= moment(date).format('ddd, DD MMM YYYY HH:mm:ss ZZ')\n    language en\n    for article in collections.articles\n      - var permalink = blog.url + '/' + article.path\n      item\n        title= article.title\n        link= permalink\n        pubDate= moment(article.date).format('ddd, DD MMM YYYY HH:mm:ss ZZ')\n        guid(isPermaLink='true')= permalink\n        author= article.author\n        description= article.snippet\n</code></pre>\n<p>The snippet above is creating a new XML document. We add a bunch of metadata properties to the XML feed for describing various things, then iterate over all the articles and output each article as an XML <code>item</code>. The one new thing you might notice is the global <code>date</code> variable, which is added by <a href=\"https://github.com/segmentio/metalsmith-build-date\">metalsmith-build-date</a> (<code>npm install metalsmith-build-date</code> and <code>.use(date())</code>). For this template to render, we will create a <code>feed.xml</code> in the <code>src</code> directory with the content below.</p>\n<pre><code>---\ntemplate: feed.jade\n---\n</code></pre>\n<h2>Adding Comments</h2>\n<p>One important aspect of a blog is having a place for readers to leave their comments and thoughts, with the goal of continuing the discussion beyond the original article. For a static site, our comment systems are little more limited to JavaScript implementations that provide their own backend for storage. Personally, I tend to gravitate toward <a href=\"http://disqus.com/\">disqus</a>. Lets add their snippet to the bottom of <code>article.jade</code>.</p>\n<pre><code class=\"language-jade\">div#disqus_thread\n\nscript.\n  //- Disqus code snippet\n  (function() {\n    var dsq = document.createElement('script'); dsq.type = 'text/javascript'; dsq.async = true;\n    dsq.src = '//blakeembrey.disqus.com/embed.js';\n    (document.getElementsByTagName('head')[0] || document.getElementsByTagName('body')[0]).appendChild(dsq);\n  })();\n</code></pre>\n<h2>Deploying to Github Pages</h2>\n<p>Now that weve got our static site running, we need to find a way to distribute it to the masses. We can host it anywhere statically without any hassle, but I opted to use <a href=\"https://pages.github.com/\">Github Pages</a>. For this to work I added a <code>CNAME</code> file to the <code>src</code> directory - this is needed for Github to allow a custom domain.</p>\n<pre><code>blakeembrey.com\n</code></pre>\n<p>Next, I created a simple deploy script and added it to my <code>package.json</code>.</p>\n<pre><code class=\"language-json.diff\"><span class=\"diff-null\">{\n  <span class=\"hljs-attr\">\"name\"</span>: <span class=\"hljs-string\">\"example-blog\"</span>,\n  <span class=\"hljs-attr\">\"version\"</span>: <span class=\"hljs-string\">\"0.0.0\"</span>,\n  <span class=\"hljs-attr\">\"private\"</span>: <span class=\"hljs-literal\">true</span>,\n  <span class=\"hljs-attr\">\"description\"</span>: <span class=\"hljs-string\">\"Example blog.\"</span>,</span>\n<span class=\"diff-addition\">  <span class=\"hljs-attr\">\"scripts\"</span>: {\n    <span class=\"hljs-attr\">\"build\"</span>: <span class=\"hljs-string\">\"node build.js\"</span>,\n    <span class=\"hljs-attr\">\"deploy\"</span>: <span class=\"hljs-string\">\"npm run build &amp;&amp; cd build &amp;&amp; git init . &amp;&amp; git add . &amp;&amp; git commit -m \\\"Deploy\\\"; git push \\\"git@github.com:blakeembrey/blakeembrey.com.git\\\" master:gh-pages --force &amp;&amp; rm -rf .git\"</span>\n  },</span>\n<span class=\"diff-null\">  <span class=\"hljs-attr\">\"author\"</span>: <span class=\"hljs-string\">\"Blake Embrey\"</span>,\n  <span class=\"hljs-attr\">\"license\"</span>: <span class=\"hljs-string\">\"MIT\"</span>\n}\n</span></code></pre>\n<p>For the build script above to work for you, youll need to change the repository (<code>blakeembrey/blakeembrey.com</code>) to your Github repository.</p>\n<h2>Extra Goodies</h2>\n<p>I also implemented a couple of bonus things in this blog during development - <a href=\"https://github.com/esundahl/metalsmith-autoprefixer\">metalsmith-autoprefixer</a> for adding browser prefixes to my CSS and <a href=\"https://github.com/aymericbeaumet/metalsmith-redirect\">metalsmith-redirect</a> for maintaining backwards compatibility with my old URLs. Check out the <a href=\"http://www.metalsmith.io/\">Metalsmith homepage</a> to discover more great plugins.</p>\n<h2>Live!</h2>\n<p>And were done, lets get this blog live. All we need to do now is execute <code>npm run deploy</code>, which will run the build and deploy scripts and push to Github. All code and content for this blog and article is <a href=\"https://github.com/blakeembrey/blakeembrey.com\">open source</a>, so feel free to browse for more ideas.</p>\n"}

