	module.exports = {"title":"Reference the Constructor of a Type in TypeScript (Generically)","date":"2015-04-28 00:00","author":"Blake Embrey","layout":"article","body":"<p>Although not initially intuitive, I run into this issue while documenting the <a href=\"http://martyjs.org/api/registry/index.html#register\"><code>register</code></a> function in Marty.js. I didnt realise a solution, so I reached out to a friend who provided me with this beauty.</p>\n<pre><code class=\"language-ts\">function register&lt;T&gt;(clazz: { new (...args: any[]): T }): T;\n</code></pre>\n<p>And it works brilliantly! But whats this even doing? Ive just created a generic function that accepts a constructor function of any type and returns that type. No need to do type coercion. It turned out perfectly for this situation, where I can register a <code>Store</code>, <code>Queries</code>, <code>ActionCreators</code> and more!</p>\n<p>You can also write this with a slightly different syntax such as <code>new (...args: any[]) =&gt; T</code>. Theres no real difference here.</p>\n<p>Although it doesnt apply here, we can also look how we might accept more specific types of constructors.</p>\n<pre><code class=\"language-ts\">// Using `typeof`...\n\nclass X {\n  method (): void {}\n}\n\nfunction create (C: typeof X) {\n  return new C()\n}\n\ncreate(X)\n\n// Using an inline parameter...\n\ninterface Y {\n  method(): void\n}\n\nfunction create2 (C: new (...args: any[]) =&gt; Y) {\n  return new C()\n}\n\nclass Z implements Y {\n  method(): void {}\n}\n\ncreate2(Z)\n</code></pre>\n"}

