	module.exports = {"title":"FaceTime Auto Answer","date":"2015-06-20 02:00","author":"Blake Embrey","layout":"article","body":"<p>I talk with my girlfriend most days. Sometimes multiple times a day. Were in a long distance relationship and, at times, completely different time zones. One habit we wanted to pick up was being able to see each other at any time.</p>\n<p>For this to work, we need to be able to auto answer each others calls. One of us could be busy working, but we still wanted to be able to see each other. After days of exploring how to make this happen, I come up with some solutions:</p>\n<ol>\n<li>Create new Skype accounts just for each other and enable auto answer on those accounts.</li>\n<li>Keep a Google Hangouts room alive that we can both join at any time.</li>\n<li>Create our own video chat platform using WebRTC that will fulfil our needs.</li>\n</ol>\n<p>Being the nerd I am, the third option sounded the most fun! From all my research, however, I was amazed no one had implemented a feature so simple into their product. Each of these options have their own drawbacks.</p>\n<ol>\n<li>I need to sign in and out between accounts (an issue for us since we work and live on our computers).</li>\n<li>Google Hangouts prompts after inactivity and kicks the user from the chat.</li>\n<li>Time.</li>\n</ol>\n<p>Eventually, I stumbled upon the magic bullet - FaceTime has an option built in to auto answer (<code>defaults write com.apple.FaceTime AutoAcceptInvitesFrom -array-add hello@blakeembrey.com</code>). It works, but it comes with even more issues. For example, when you receive a call there is a black overlay over the video call which makes visibility difficult. On a second test, we discovered that it rings infinitely. Well, so much for that.</p>\n<p>At this point, we gave up and Keyue needed to sleep. I decided to continue research the following day when she was at work (my night time) and discovered some people had written an AppleScript for this years ago. I stayed up until 4am trying to create a new AppleScript, hacking away in a forgiving syntax I had no idea how to use without the use of Google.</p>\n<h2>Implementation</h2>\n<p>Lets start by opening the Script Editor application. Copy and paste the following code into the editor, changing the caller id check to your desired caller. This the caller that is shown next to the profile image and accept button during ringing. For me, Keyue Bao is a contact so I can use her name. If they arent a contact, youll need to type the specific email or phone number here.</p>\n<pre><code class=\"language-applescript\"><span class=\"hljs-keyword\">set</span> contacts <span class=\"hljs-keyword\">to</span> {<span class=\"hljs-string\">\"Keyue Bao\"</span>}\n\n<span class=\"hljs-keyword\">repeat</span>\n  <span class=\"hljs-keyword\">repeat</span> <span class=\"hljs-keyword\">while</span> <span class=\"hljs-built_in\">application</span> <span class=\"hljs-string\">\"FaceTime\"</span> <span class=\"hljs-keyword\">is</span> <span class=\"hljs-built_in\">running</span>\n    <span class=\"hljs-keyword\">tell</span> <span class=\"hljs-built_in\">application</span> <span class=\"hljs-string\">\"System Events\"</span>\n      <span class=\"hljs-keyword\">tell</span> process <span class=\"hljs-string\">\"FaceTime\"</span>\n        <span class=\"hljs-keyword\">set</span> acceptButton <span class=\"hljs-keyword\">to</span> <span class=\"hljs-keyword\">a reference</span> <span class=\"hljs-keyword\">to</span> (button <span class=\"hljs-string\">\"Accept\"</span> <span class=\"hljs-keyword\">of</span> window <span class=\"hljs-number\">1</span>)\n\n        <span class=\"hljs-keyword\">if</span> acceptButton exists <span class=\"hljs-keyword\">then</span>\n          <span class=\"hljs-keyword\">set</span> callerId <span class=\"hljs-keyword\">to</span> value <span class=\"hljs-keyword\">of</span> static <span class=\"hljs-built_in\">text</span> <span class=\"hljs-number\">2</span> <span class=\"hljs-keyword\">of</span> window <span class=\"hljs-number\">1</span>\n\n          <span class=\"hljs-keyword\">if</span> contacts <span class=\"hljs-keyword\">contains</span> callerId <span class=\"hljs-keyword\">then</span>\n            click acceptButton\n          <span class=\"hljs-keyword\">end</span> <span class=\"hljs-keyword\">if</span>\n        <span class=\"hljs-keyword\">end</span> <span class=\"hljs-keyword\">if</span>\n      <span class=\"hljs-keyword\">end</span> <span class=\"hljs-keyword\">tell</span>\n    <span class=\"hljs-keyword\">end</span> <span class=\"hljs-keyword\">tell</span>\n    <span class=\"hljs-built_in\">delay</span> <span class=\"hljs-number\">2</span>\n  <span class=\"hljs-keyword\">end</span> <span class=\"hljs-keyword\">repeat</span>\n  <span class=\"hljs-built_in\">delay</span> <span class=\"hljs-number\">5</span>\n<span class=\"hljs-keyword\">end</span> <span class=\"hljs-keyword\">repeat</span>\n</code></pre>\n<p>The script runs on an infinite loop. There is a delay of 5 seconds when the application is closed and 2 seconds when the application is open. It attempts to select the answer button on the screen and, if it exists, it checks the caller id and clicks answer. Pretty simple.</p>\n<p>Next we want to export the script for execution. Navigate to <code>File -&gt; Export</code> and save it somewhere you can remember it. For me, it was as <code>Documents/Scripts/facetime-auto-answer</code>. You need this path for the next step.</p>\n<p>Lets create a Launchd script to handle execution on computer start up. Navigate to <code>~/Library/LaunchAgents</code> using Terminal (open Terminal and enter <code>cd ~/Library/LaunchAgents</code>). After that, thats add our file (below) as <code>com.blakeembrey.facetime-auto-answer.plist</code> (just <code>vi com.blakeembrey.facetime-auto-answer.plist</code> and hit <code>i</code> to switch to insert mode).</p>\n<pre><code class=\"language-xml\"><span class=\"php\"><span class=\"hljs-meta\">&lt;?</span>xml version=<span class=\"hljs-string\">\"1.0\"</span> encoding=<span class=\"hljs-string\">\"UTF-8\"</span><span class=\"hljs-meta\">?&gt;</span></span>\n<span class=\"hljs-meta\">&lt;!DOCTYPE plist PUBLIC \"-//Apple//DTD PLIST 1.0//EN\" \"http://www.apple.com/DTDs/PropertyList-1.0.dtd\"&gt;</span>\n<span class=\"hljs-tag\">&lt;<span class=\"hljs-name\">plist</span> <span class=\"hljs-attr\">version</span>=<span class=\"hljs-string\">\"1.0\"</span>&gt;</span>\n<span class=\"hljs-tag\">&lt;<span class=\"hljs-name\">dict</span>&gt;</span>\n  <span class=\"hljs-tag\">&lt;<span class=\"hljs-name\">key</span>&gt;</span>Label<span class=\"hljs-tag\">&lt;/<span class=\"hljs-name\">key</span>&gt;</span>\n  <span class=\"hljs-tag\">&lt;<span class=\"hljs-name\">string</span>&gt;</span>com.blakeembrey.FaceTimeAutoAnswer<span class=\"hljs-tag\">&lt;/<span class=\"hljs-name\">string</span>&gt;</span>\n  <span class=\"hljs-tag\">&lt;<span class=\"hljs-name\">key</span>&gt;</span>Program<span class=\"hljs-tag\">&lt;/<span class=\"hljs-name\">key</span>&gt;</span>\n  <span class=\"hljs-tag\">&lt;<span class=\"hljs-name\">string</span>&gt;</span>/usr/bin/osascript<span class=\"hljs-tag\">&lt;/<span class=\"hljs-name\">string</span>&gt;</span>\n  <span class=\"hljs-tag\">&lt;<span class=\"hljs-name\">key</span>&gt;</span>ProgramArguments<span class=\"hljs-tag\">&lt;/<span class=\"hljs-name\">key</span>&gt;</span>\n  <span class=\"hljs-tag\">&lt;<span class=\"hljs-name\">array</span>&gt;</span>\n    <span class=\"hljs-tag\">&lt;<span class=\"hljs-name\">string</span>&gt;</span>osascript<span class=\"hljs-tag\">&lt;/<span class=\"hljs-name\">string</span>&gt;</span>\n    <span class=\"hljs-tag\">&lt;<span class=\"hljs-name\">string</span>&gt;</span>/Users/blakeembrey/Scripts/facetime-auto-answer.scpt<span class=\"hljs-tag\">&lt;/<span class=\"hljs-name\">string</span>&gt;</span>\n  <span class=\"hljs-tag\">&lt;/<span class=\"hljs-name\">array</span>&gt;</span>\n  <span class=\"hljs-tag\">&lt;<span class=\"hljs-name\">key</span>&gt;</span>RunAtLoad<span class=\"hljs-tag\">&lt;/<span class=\"hljs-name\">key</span>&gt;</span>\n  <span class=\"hljs-tag\">&lt;<span class=\"hljs-name\">true</span>/&gt;</span>\n  <span class=\"hljs-tag\">&lt;<span class=\"hljs-name\">key</span>&gt;</span>KeepAlive<span class=\"hljs-tag\">&lt;/<span class=\"hljs-name\">key</span>&gt;</span>\n  <span class=\"hljs-tag\">&lt;<span class=\"hljs-name\">true</span>/&gt;</span>\n<span class=\"hljs-tag\">&lt;/<span class=\"hljs-name\">dict</span>&gt;</span>\n<span class=\"hljs-tag\">&lt;/<span class=\"hljs-name\">plist</span>&gt;</span>\n</code></pre>\n<p>Make sure you adjust the argument above to the correct path from the export step. Once its correct, exit back to the terminal (<code>Esc</code>, <code>:wq</code>, <code>Enter</code>) and make Launchd pick up the new script without rebooting.</p>\n<pre><code>launchctl load -w com.blakeembrey.facetime-auto-answer.plist\n</code></pre>\n<p>Youll need to accept the accessibility dialog (open system preferences and enable access). We can quickly check that the script is running by entering <code>launchctl list | grep facetime-auto-answer</code>. And were done.</p>\n<h2>Conclusion</h2>\n<p>FaceTime is really nice for video chatting. It disables automatically when youre in a different window (I regularly use up to 6 windows during a work day for different tasks) and the interface is very simple to use. The video quality is great and, when the call drops temporarily it will automatically join the call again (with the video still enabled - looking at you Skype!). On top of that, it has a low CPU footprint, especially compared with Skype.</p>\n<p>However, theres a couple of major issues with it. First off, it makes the rest of the computer quieter which makes it difficult to work or do anything else while on the call. For example, we cant really watch a movie together (audio is dimmed). The second is a feature request. I would love the floating window feature from Skype.</p>\n<p>And were done. Im going to tweak the same script to auto answer Skype next. Hopefully this comes built in with future versions of video chat clients. Its such a basic feature that its exclusion makes me ponder the disconnect between the teams creating the software and reality.</p>\n"}

