	module.exports = {"title":"Skype Auto Answer with AppleScript","date":"2015-06-22 02:00","author":"Blake Embrey","layout":"article","body":"<p>After discovering the ability to <a href=\"/articles/2015/06/facetime-auto-answer-applescript/\">auto answer FaceTime</a>, I tried my hand at adapting the script for Skype. I waited a couple of days until my girlfriend had some downtime and patience to call me over and over while I tried to debug the elements on my screen. Eventually I figured out how to select the window, and then the correct button. I did try, unsuccessfully, for a long time to select the button based on the label. If anyone knows how to do this, please leave a comment!</p>\n<p>Continuing on, set up is much the same with only the basic code changed. Lets start again by opening up Script Editor and copying this snippet into the editor:</p>\n<pre><code class=\"language-applescript\"><span class=\"hljs-keyword\">set</span> contacts <span class=\"hljs-keyword\">to</span> {<span class=\"hljs-string\">\"Keyue Bao\"</span>}\n\n<span class=\"hljs-keyword\">repeat</span>\n  <span class=\"hljs-keyword\">repeat</span> <span class=\"hljs-keyword\">while</span> <span class=\"hljs-built_in\">application</span> <span class=\"hljs-string\">\"Skype\"</span> <span class=\"hljs-keyword\">is</span> <span class=\"hljs-built_in\">running</span>\n    <span class=\"hljs-keyword\">tell</span> <span class=\"hljs-built_in\">application</span> <span class=\"hljs-string\">\"System Events\"</span>\n      <span class=\"hljs-keyword\">tell</span> process <span class=\"hljs-string\">\"Skype\"</span>\n        <span class=\"hljs-keyword\">set</span> videoCallWindow <span class=\"hljs-keyword\">to</span> <span class=\"hljs-keyword\">a reference</span> <span class=\"hljs-keyword\">to</span> (<span class=\"hljs-keyword\">first</span> window <span class=\"hljs-keyword\">whose</span> <span class=\"hljs-built_in\">name</span> <span class=\"hljs-keyword\">is</span> <span class=\"hljs-string\">\"Incoming Video Call\"</span>)\n\n        <span class=\"hljs-keyword\">if</span> videoCallWindow exists <span class=\"hljs-keyword\">then</span>\n          <span class=\"hljs-keyword\">set</span> callerId <span class=\"hljs-keyword\">to</span> value <span class=\"hljs-keyword\">of</span> static <span class=\"hljs-built_in\">text</span> <span class=\"hljs-number\">1</span> <span class=\"hljs-keyword\">of</span> videoCallWindow\n\n          <span class=\"hljs-keyword\">if</span> contacts <span class=\"hljs-keyword\">contains</span> callerId <span class=\"hljs-keyword\">then</span>\n            click button <span class=\"hljs-number\">2</span> <span class=\"hljs-keyword\">of</span> videoCallWindow\n          <span class=\"hljs-keyword\">end</span> <span class=\"hljs-keyword\">if</span>\n        <span class=\"hljs-keyword\">end</span> <span class=\"hljs-keyword\">if</span>\n      <span class=\"hljs-keyword\">end</span> <span class=\"hljs-keyword\">tell</span>\n    <span class=\"hljs-keyword\">end</span> <span class=\"hljs-keyword\">tell</span>\n    <span class=\"hljs-built_in\">delay</span> <span class=\"hljs-number\">2</span>\n  <span class=\"hljs-keyword\">end</span> <span class=\"hljs-keyword\">repeat</span>\n  <span class=\"hljs-built_in\">delay</span> <span class=\"hljs-number\">5</span>\n<span class=\"hljs-keyword\">end</span> <span class=\"hljs-keyword\">repeat</span>\n</code></pre>\n<p>So, while Skype is running we try to select the incoming video call window. Of course, this window will only exist when we have a call to answer, so we check that it exists. After that, we grab the caller ID from the text element and check if it matches who we want. Then we click answer. It actually took me a few tried to figure out which index was the answer with video button as it doesnt follow visual order.</p>\n<p>Before we export, change the caller ID. Then, go to <code>File -&gt; Export</code> and save it somewhere youll remember. I exported mine to <code>Documents/Scripts/skype-auto-answer</code>.</p>\n<p>Next step, lets navigate to <code>cd ~/Library/LaunchAgents</code> in the Terminal and create our file using <code>vi com.blakeembrey.skype-auto-answer.plist</code>. In vim, youll need to press <code>i</code> to go into insert mode. Copy and paste the code below, but remember to change the script location to where you just exported to.</p>\n<pre><code class=\"language-xml\"><span class=\"php\"><span class=\"hljs-meta\">&lt;?</span>xml version=<span class=\"hljs-string\">\"1.0\"</span> encoding=<span class=\"hljs-string\">\"UTF-8\"</span><span class=\"hljs-meta\">?&gt;</span></span>\n<span class=\"hljs-meta\">&lt;!DOCTYPE plist PUBLIC \"-//Apple//DTD PLIST 1.0//EN\" \"http://www.apple.com/DTDs/PropertyList-1.0.dtd\"&gt;</span>\n<span class=\"hljs-tag\">&lt;<span class=\"hljs-name\">plist</span> <span class=\"hljs-attr\">version</span>=<span class=\"hljs-string\">\"1.0\"</span>&gt;</span>\n<span class=\"hljs-tag\">&lt;<span class=\"hljs-name\">dict</span>&gt;</span>\n  <span class=\"hljs-tag\">&lt;<span class=\"hljs-name\">key</span>&gt;</span>Label<span class=\"hljs-tag\">&lt;/<span class=\"hljs-name\">key</span>&gt;</span>\n  <span class=\"hljs-tag\">&lt;<span class=\"hljs-name\">string</span>&gt;</span>com.blakeembrey.skype-auto-answer<span class=\"hljs-tag\">&lt;/<span class=\"hljs-name\">string</span>&gt;</span>\n  <span class=\"hljs-tag\">&lt;<span class=\"hljs-name\">key</span>&gt;</span>Program<span class=\"hljs-tag\">&lt;/<span class=\"hljs-name\">key</span>&gt;</span>\n  <span class=\"hljs-tag\">&lt;<span class=\"hljs-name\">string</span>&gt;</span>/usr/bin/osascript<span class=\"hljs-tag\">&lt;/<span class=\"hljs-name\">string</span>&gt;</span>\n  <span class=\"hljs-tag\">&lt;<span class=\"hljs-name\">key</span>&gt;</span>ProgramArguments<span class=\"hljs-tag\">&lt;/<span class=\"hljs-name\">key</span>&gt;</span>\n  <span class=\"hljs-tag\">&lt;<span class=\"hljs-name\">array</span>&gt;</span>\n    <span class=\"hljs-tag\">&lt;<span class=\"hljs-name\">string</span>&gt;</span>osascript<span class=\"hljs-tag\">&lt;/<span class=\"hljs-name\">string</span>&gt;</span>\n    <span class=\"hljs-tag\">&lt;<span class=\"hljs-name\">string</span>&gt;</span>/Users/blakeembrey/Documents/Scripts/skype-auto-answer.scpt<span class=\"hljs-tag\">&lt;/<span class=\"hljs-name\">string</span>&gt;</span>\n  <span class=\"hljs-tag\">&lt;/<span class=\"hljs-name\">array</span>&gt;</span>\n  <span class=\"hljs-tag\">&lt;<span class=\"hljs-name\">key</span>&gt;</span>RunAtLoad<span class=\"hljs-tag\">&lt;/<span class=\"hljs-name\">key</span>&gt;</span>\n  <span class=\"hljs-tag\">&lt;<span class=\"hljs-name\">true</span>/&gt;</span>\n  <span class=\"hljs-tag\">&lt;<span class=\"hljs-name\">key</span>&gt;</span>KeepAlive<span class=\"hljs-tag\">&lt;/<span class=\"hljs-name\">key</span>&gt;</span>\n  <span class=\"hljs-tag\">&lt;<span class=\"hljs-name\">true</span>/&gt;</span>\n<span class=\"hljs-tag\">&lt;/<span class=\"hljs-name\">dict</span>&gt;</span>\n<span class=\"hljs-tag\">&lt;/<span class=\"hljs-name\">plist</span>&gt;</span>\n</code></pre>\n<p>And were almost done. Lets exit vim (<code>Esc</code>, <code>:wq</code>, <code>Enter</code>) and get Launchd to pick up our changes.</p>\n<pre><code>launchctl load -w com.blakeembrey.skype-auto-answer.plist\n</code></pre>\n<p>Tada! You might need to accept the accessibility dialog that pops up, but the script will now be running. You can verify this by executing <code>launchctl list | grep skype-auto-answer</code>.</p>\n<p>Done!</p>\n<p>P.S. If you ever want to unload the script, just execute <code>launchctl unload -w com.blakeembrey.skype-auto-answer.plist</code>, same as you did to load.</p>\n"}

