	module.exports = {"title":"Introduction to Free Style (and CSS-in-JS)","date":"2016-01-17 14:45","author":"Blake Embrey","layout":"article","github":"blakeembrey/free-style","npm":"free-style","body":"<p>With the release of Free Style 1.0, I figure its about time to write about Free Style - how it works, why youd want to use it and little introduction to CSS-in-JS. This has been a long time coming, with my first commit to Free Style over a year ago, and the first commit to Free Style in its current form 10 months ago. This is not a blog post designed to sway decisions - as always, you should use your own fair judgement.</p>\n<h2>CSS-in-JS</h2>\n<p>The idea of CSS-in-JS is well covered in <a href=\"https://speakerdeck.com/vjeux/react-css-in-js\">this presentation by React engineer</a>, Christopher Chedeau, and by <a href=\"https://github.com/MicheleBertoli/css-in-js\">many</a> <a href=\"http://formidable.com/blog/2015/03/01/launching-radium/\">others</a>, so Ill be brief. As React popularized the declarative DOM, it also enabled a generation of CSS-in-JS approaches that attempt to solve the many pitfalls of CSS. These pitfalls are well known and documented, and including features such as the global namespace, constant sharing and many approaches to component isolation (BEM, SMACSS). Writing CSS in a way that avoids the pitfalls can be regarded an art.</p>\n<p>CSS-in-JS approaches exist to solve the pollution of the global namespace, constant sharing, component isolation, and bring many other unforeseen benefits. The JS part exists because these solutions utilize JavaScript as the way to provide namespacing, constant sharing and proper isolation. You may have already known, but these are things that have long been solved in programming languages, including JavaScript - with CommonJS, AMD and recently ES6 modules. It stands to reason that, if possible, JavaScript will provide a more sound foundation for writing modular CSS. Tooling for JavaScript is more mature, with the ability to do autocompletion, dead code elimination and linting common-place.</p>\n<h2>How and Why Does Free Style Work?</h2>\n<p>Free Style works with hashes. If theres one word you should love at the end of this section, its hashing. With that said, the essence of <a href=\"https://github.com/blakeembrey/free-style/blob/master/src/free-style.ts\">free-style</a> is less than 500 lines of code (in TypeScript), so I definitely suggest you check it out.</p>\n<p>Free Style is built on top of a core <code>Cache</code> class. This class implements a way to append children using an ID (which is a hash), keeps track of how many times a child was added and removed, and can also attach simple change listeners (for when a child is added or removed). Three classes extend the <code>Cache</code> implementation to replicate the structure of CSS - <code>Rule</code>, <code>Style</code> and <code>FreeStyle</code>. The only other important class is <code>Selector</code>, which implements a <code>Cacheable</code> interface (<code>Cache</code> fulfills the same interface).</p>\n<p>Using these four classes, we can replicate CSS in a way that automatically de-dupes styles. First, we create a <code>FreeStyle</code> instance (it can be imagined as a <code>.css</code> file). This class holds <code>Rule</code> and <code>Style</code> children. When you use <code>registerStyle</code>, itll stringifying each object to CSS and hash the contents, while also propagating any rules upward (E.g. when <code>@media</code> nested inside a style). The result is a single style registering (potentially) a number of <code>Style</code> and <code>Rule</code> instances, all of which have their hashes of their own contents. Throughout each instance creation, <code>registerStyle</code> collects a separate hash that is returned as the CSS class name to use. Finally, when the final class name hash is known, the class name is interpolated with all selectors and returned for you to use.</p>\n<p>The result of this means that duplicate hashes are automatically merged. A duplicate hash means a duplicate rule, style or selector. The benefit of separating <code>Rule</code> and <code>Style</code> means that two identical media queries can be merged together (less CSS output) and so can identical styles within each context (E.g. identical styles inside and outside the media query can not be merged, but duplicates both inside or outside can be). The <code>Selector</code> class exists because now that duplicates are merged, multiple selectors can exist for the same <code>Style</code>.</p>\n<p>The other interesting methods are <code>registerRule</code> and <code>registerKeyframes</code>. Both work similar to <code>registerStyle</code>, but are much be simpler. <code>registerRule</code> works by recursively registering rules, which are automatically being hashed based on the rule and their contents. <code>registerKeyframes</code> works by creating rules and styles that get added to a <code>Rule</code> instance and returns a selector of the complete hashed contents (hence keyframes are automatically hashed/namespaced).</p>\n<p>All this hashing results in the fact that all styles are automatically unique. Registered styles and keyframes have a hash to identify them and the chance of a conflicting style is now left to the computer to resolve, not you. The other pitfalls of CSS are automatically solved as the result of JavaScript, as the hash can only be known and exposed by the implementor while constants and isolation are now solved (you can even use NPM libraries for style manipulation now).</p>\n<p><strong>Update:</strong> One useful fact that may not be immediately obvious. By using hashes as the class name, it means output is <em>always</em> consistent across front-end and back-end (E.g. in isomorphic applications).</p>\n<h2>Free Style Output Targets</h2>\n<p>Now that you understand how Free Style works, the output targets should make a lot more sense. By default, Free Style exposes a feature-rich implementation ready for third-parties to build on top. To use it today, you must create instances of <code>FreeStyle</code> (using <code>create()</code>), merge any other instances and use <code>getStyles</code> to get the CSS output. Theres an <code>inject()</code> method, which will take the result of <code>getStyles</code> and wrap it in <code>&lt;style /&gt;</code> in the <code>&lt;head /&gt;</code>.</p>\n<p>Currently, there are two other implementations of output targets. The first is <a href=\"https://github.com/jkroso/easy-style\"><code>easy-style</code></a>, a simple wrapper around the complex functionality in Free Style that abstracts away multiple <code>FreeStyle</code> instances. It exposes three core methods - <code>style</code>, <code>rule</code>, <code>keyframe</code> - avoiding the concept of multiple instances, which makes it suitable for most web-apps. Theres also <a href=\"https://github.com/blakeembrey/react-free-style\"><code>react-free-style</code></a>, which extends the core <code>FreeStyle</code> class with the ability to wrap React components and use Reacts <code>context</code> for collecting all the styles used in the application. This is an interesting feature with interesting repercussions, such as only styles for the components on screen will be output in CSS (useful for minimizing the transfer size of isomorphic applications).</p>\n<h2>Other CSS-in-JS Solutions</h2>\n<p>A number of other CSS-in-JS solutions also exist, including <a href=\"https://github.com/FormidableLabs/radium\"><code>radium</code></a>, <a href=\"https://github.com/js-next/react-style\"><code>react-style</code></a> and <a href=\"https://github.com/jsstyles/jss\"><code>jss</code></a>. Radium takes an all JavaScript approach, while <code>jss</code> and <code>react-style</code> are more familiar. Where <code>jss</code> and <code>react-style</code> differ to <code>free-style</code> is the approaches they take. Both went for namespacing in CSS by generating unique names instead of hashes. They also both went with <code>StyleSheet</code> instances that you create once, while <code>free-style</code> makes you register each style individually (and for a good reason, it allows linters to detect when a style is no longer used as itll appear as dead code). They may also restrict some subset of CSS that you can actually use for different reasons. As far as I can tell, neither go too much further into CSS tooling concepts such as style de-duping and minifying, which <code>free-style</code> gives you for free with hashes.</p>\n"}

